<template>
	<div class="more-service-one-active-success">
		<tip-page :tipTitle="tipTitle" tipButton="返回信用卡首页" @submit="back"></tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			tipTitle: ''
		};
	},
	created() {
		if(this.$route.params.fromAdd){
			this.tipTitle = '卡片已激活且添加成功';
		} else{
			this.tipTitle = '卡片激活成功';
		}
	},
	methods: {
		back() {
			this.$router.push({name: 'Credit'});
		}
	}
};

</script>

<style lang="scss">
.more-service-one-active-success{
}
</style>


