<template>
    <div class="public-index">
        <div class="field-part">
            <cell-map :dataCopy="infoData" :map="map"></cell-map>
        </div>

        <div class="field-part">
            <cell-password :options=" cellPassword "></cell-password>
        </div>

        <div class="forget-pwd">
            忘记查询密码
        </div>
    </div>
</template>

<script>
    export default {
        name: 'public-index',
        props: ['dataCopy', 'mapCopy'],
        data() {
            return {
                cellPassword: {
                    title: '交易密码',
                    rule: 'number' // number or mix
                },
                password: ''
            };
        },
        computed: {
            infoData() {
                return this.dataCopy;
            },
            map() {
                return this.mapCopy;
            }
        }
    };

</script>

<style lang="scss" scoped>
    .forget-pwd {
        margin: 20px 50px 0;
        float: right;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #308CF8;
        line-height: 36px;
    }
</style>


