<template>
	<div class="auto-repay-not-open">
        <div class="cell-part">
    		<mt-cell title="自动还款">
    			<mt-switch v-model="isOpen"></mt-switch>
    		</mt-cell>
        </div>
		<div v-if="!isOpen" class="field-hint">
			开通自动还款功能后，将在信用卡账单还款日，自动从指定的还款账号中扣缴信用卡账款
		</div>
        <template  v-if="isOpen">
    		<div class="cell-part">
    			<mt-cell class="allow-down" @click.native="showSelectorPaymentAccount()" title="自动还款账号：" placeholder="" :value="PAYEE_ACCOUNT" is-link>
            	</mt-cell>
            	<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
                <mt-cell class="allow-down" title="自动还款方式" @click.native=" showSelectorAction()  "is-link :value=" REPAT_TYPE || '请选择' ">
                </mt-cell>
            </div>
    	    <div class="cell-part">
                <cell-password :options=" cellPassword "></cell-password>
            </div>
            <div class="field-warn text-right">
            	<a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
            </div>
            <div class="button-bottom">
            	<mt-button @click="submit" type="primary" size="large">确认开通</mt-button>
            </div>
            <selector-action ref="selectorAction" :options=" selectorAction "></selector-action>
        </template>
	</div>
</template>

<script>

import service from '@/services/credit/manage';
import * as commService from '@/services/bank/selector';

export default {
	data() {
		return {
			isOpen: false,
			PAYEE_ACCOUNT: '',
			REPAT_TYPE: '全款还款',
            REPAT_TYPE_ID: '1',
			submitStatus: false,
			selectorPaymentAccount: {
                checked: {},
                showAddBtn: true,
                showWarn: false,
                accountList: []
            },
            cellPassword: {
                title: '查询密码：',
                rule: 'number'
            },
            selectorAction: {
                title: '请选择自动还款方式',
                key: 'field1',
                value: 'field2',
                checked: '',
                actionList: [
                    {
                        field1: 1,
                        field2: '全款还款'
                    },
                    {
                        field1: 2,
                        field2: '部分还款'
                    }
                ]
            }
		};
	},
    created() {
        commService.getAccountList().then((res) => {
            this.selectorPaymentAccount.accountList = res.data;
            let defaultAccount = res.data.filter(( item )=>{ return item.isDefault })[0] || {};
            this.selectorPaymentAccount.checked = defaultAccount;
            this.PAYEE_ACCOUNT = defaultAccount.ACCOUNT;
        }, (err) => {
            console.log( err );
        });
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.PAYEE_ACCOUNT = data.item.ACCOUNT;
            }, data => {
            });
        },
        toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
        submit() {
            service.setAutoRepay({
                PAYER_ACCOUNT: this.PAYER_ACCOUNT,
                OPTION: '3',
                PAYEE_ACCOUNT: this.PAYEE_ACCOUNT,
                REPAT_TYPE: this.REPAT_TYPE_ID,
                PAY_PWD: ''
            })
            .then((data) => {
                this.$router.push({name: 'CreditManageAutoRepaySuccess', params: {type: '开通'}});
            }, (err) => {
                console.log(err);
            });
        },
        showSelectorAction() {
            this.$refs.selectorAction.show().then((data) => {
                this.selectorAction.checked = data.item.field1;
                this.REPAT_TYPE = data.item.field2;
                this.REPAT_TYPE_ID = data.item.field1;
            }, (data) => {
            });
        }
	}
};

</script>

<style lang="scss">

.auto-repay-not-open{
	.mint-cell-title,{
		width: 3.6rem;
	}
	.mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
    .forget-pwd{
		color: #308cf8;
		font-size: 13px;
	}
}

</style>


