<template>
    <div class="bill">
        <div class="field-part">
            <mt-field label="信用卡卡号" v-model="formData.PAYER_ACCOUNT" placeholder="仅限安徽农金系统内的信用卡"></mt-field>
            <mt-field label="持卡人姓名" class="card-name" v-model="formData.ACCT_NAME" placeholder="请输入信用卡持卡人姓名">
                <i v-if="nameEmpty" class="icon-contact"></i>
            </mt-field>
        </div>

        <div class="field-part">
            <account-select title='借记卡卡号' :value='formData.PAYEE_ACCOUNT' :balance="formData.PAYEE_ACCOUNT_BALANCE" @click.native="showDebitAccount()"></account-select>
            <selector-payment-account ref="debitAccount" :options=" debitAccount "></selector-payment-account>
        </div>

        <div class="field-part">
            <cell-transfer-money title="还款金额" placeholder="请输入还款金额" validate="还款金额大于借记卡金额" :totalMoney="debitAccount.checked.balance"
                v-model="formData.AMOUNT">
            </cell-transfer-money>
        </div>

        <div class="field-action">
            <mt-button type="primary" :disabled="!clickable" size="large" @click="next">下一步</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/bank/selector';
    export default {
        name: 'bill',
        data() {
            return {
                formData: {
                    PAYER_ACCOUNT: '',
                    ACCT_NAME: '',
                    ACCT_TYPE: 'credit',
                    PAYEE_ACCOUNT: '',
                    COR_NAME: '',
                    INCARDTYPE: '',
                    PAYEE_ACCOUNT_BALANCE: '',
                    AMOUNT: ''
                },
                infoData: {},
                debitAccount: {
                    title: '选择借记卡',
                    type: 'payment',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                },
                nameEmpty: true,
                clickable: false
            };
        },
        watch: {
            'formData': {
                handler: 'validate',
                deep: true
            }
        },
        created() {
            let ls = this.$ls.getItem('credit-repayment-other');
            if (ls != null) {
                this.formData.PAYER_ACCOUNT = ls.PAYER_ACCOUNT;
                this.formData.ACCT_NAME = ls.ACCT_NAME;
                this.formData.ACCT_TYPE = ls.ACCT_TYPE;
                this.formData.AMOUNT = ls.AMOUNT;
            }
            services.getAccountList()
                .then((res) => {
                    this.debitAccount.accountList = res.data;
                    if (ls == null) {
                        let _data = res.data.filter(item => {
                            return item.isDefault === true;
                        })[0] || {};
                        this.debitAccount.checked = _data;
                        this.formData.PAYEE_ACCOUNT = _data.ACCOUNT;
                        this.formData.COR_NAME = _data.name;
                        this.formData.INCARDTYPE = _data.TYPE;
                        this.formData.PAYEE_ACCOUNT_BALANCE = _data.balance;
                    } else {
                        let _data = res.data.filter(item => {
                            return item.ACCOUNT === ls.PAYEE_ACCOUNT;
                        })[0] || {};
                        this.debitAccount.checked = _data;
                        this.formData.PAYEE_ACCOUNT = ls.PAYEE_ACCOUNT;
                        this.formData.COR_NAME = ls.COR_NAME;
                        this.formData.INCARDTYPE = ls.INCARDTYPE;
                        this.formData.PAYEE_ACCOUNT_BALANCE = ls.PAYEE_ACCOUNT_BALANCE;
                    }
                }, (err) => {
                    console.log(err);
                });
        },
        methods: {
            showDebitAccount() {
                this.$refs.debitAccount.show().then((data) => {
                    this.debitAccount.checked = data.item;
                    this.formData.PAYEE_ACCOUNT = this.debitAccount.checked.ACCOUNT;
                    this.formData.COR_NAME = this.debitAccount.checked.name;
                    this.formData.INCARDTYPE = this.debitAccount.checked.TYPE;
                    this.formData.PAYEE_ACCOUNT_BALANCE = this.debitAccount.checked.balance;
                }, (data) => {
                });
            },
            validate(val) {
                if (this.$validate.isEmpty(val.ACCT_NAME)) {
                    this.clickable = false;
                    this.nameEmpty = true;
                    return false;
                } else {
                    this.nameEmpty = false;
                }
                if (this.$validate.isEmpty(val.PAYER_ACCOUNT)) {
                    this.clickable = false;
                    return false;
                }
                if (this.$validate.isEmpty(val.AMOUNT)) {
                    this.clickable = false;
                    return false;
                }
                this.clickable = true;
            },
            next() {
                console.log(this.formData);
                this.$router.push({ name: 'CreditRepaymentOtherPay' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName']) {
                this.$ls.setItem('credit-repayment-other', this.formData);
            } else {
                this.$ls.removeItem('credit-repayment-other', this.formData);
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .field-action {
        margin-top: 80px;
    }

    .card-name {
        position: relative;

        .icon-contact {
            position: absolute;
            top: 0px;
            right: 0px;
            height: 44px;
            width: 42px;
            @include image-set('../../../../assets/img/selector/contacts');
            background-repeat: no-repeat;
            background-size: 100% 100%;
            transform: translateY(-50%);
        }
    }
</style>


