var str = ' <div style="padding: 5px;"><h5>杜鹃信用卡惠消费分期业务条款及细则</h5>' + '<p>若安徽农村商业银行系统杜鹃信用卡持卡人同意申请惠消费分期业务，' + '即表示其已阅读并同意遵守本条款及细则：</p>' + '<p>1、杜鹃信用卡惠消费分期业务指持卡人对单笔刷卡消费的交易，在账单生成前提出申请，' + '并按约定的期数将消费金额分期归还给银行。持卡人未出账单中单笔消费交易达到分期金额' + '条件<b>（单笔消费金额500元以上）</b>，可在交易入账日后至下一账单日前，将该笔消费交易全额申请惠消费分期。' + '办理分期时，持卡人不可任意选定或更改分期交易金额。惠消费分期期限支持<b>3期、6期、9期、' + '12期、18期、24期</b>，持卡人可自由选择。该业务仅限杜鹃信用卡主卡办理，附属卡不支持办理。</p>' + '<p>2、下列特定类型交易不适用于分期业务：</p><p>（1）依照适用的信用卡章程或领用合约的规定收取的' + '透支利息和各项手续费，如年费、取现手续费、违约金等；</p><p>（2）转账或取现交易；' + '</p><p>（3）房产类交易以及其他监管机构或发卡机构不时指定的类别交易或含以上交易的消费欠款。</p>' + '<p>3、持卡人申请占用信用额度的各类分期业务的合计金额不能高于申请分期付款时该信</p>' + '用卡的固定信用额度，持卡人在信用卡账户内的存款和临时调高的信用额度均不能增加用于分</p>' + '期业务交易的额度。' + '<p>4、持卡人可在发卡机构允许的范围内申请分期期数，每月为一期，' + '自申请成功后的第一期账单起算。</p>' + '<p>5、每期还款本金依持卡人选择并经发卡机构批准同意的分期期数按每月为一期平均摊还。手续费依分期期数而定，' + '按月收取，手续费费率以持卡人申请分期时发卡机构告知为准。发卡机构在按监管要求完成相' + '关价格报备和公告等必要手续后，可调整相关手续费率。</p>' + '<p>6、分期业务存续期间，当期分期入账金额及手续费不享受最低还款额待遇，须每月全额还款，' + '如因持卡人未及时全额还款导致违约金、利息等相关费用需由持卡人自行承担。</p>' + '<p>7、成功申请分期后，持卡人的可用信用额度根据每月实际归还的分期本金等额恢复。</p>' + '<p>8、持卡人成功申请分期后，不可对已申请成功的分期付款的分期期数、金额进行更改>' + '也不能对已转入分期计划的金额再次申请分期付款。</p>' + '<p>9、持卡人可以通过安徽农村商业银行营业网点、网上银行等自助渠道，或致电卡片背面所示' + '24小时客服热线申请提前终止已经申请成功的分期付款业务，仍须支付提前还款手续费。</p>' + '<p>10、对于已经成功申请分期付款业务的交易若发生退货情况，仅在持卡人提出主动申请后，' + '发卡机构才会终止其分期付款业务，持卡人已经支付的各期手续费不予退还。</p>' + '<p>11、发卡机构保留不允许任何违法交易、不良信用卡账户、状态不正常的信用卡账户，' + '或发卡机构因任何理由认为不适合申请分期付款业务的信用卡账户申请本业务的权利。</p>' + '<p>12、持卡人的信用卡账户若在分期付款期间由于账户销户、注销卡片、' + '账户被冻结等任何原因变为状态不正常，或发卡机构因任何理由认为其资信状况发生恶化，' + '导致该账户不再适合进行分期付款业务，则发卡机构无须任何通知，' + '该笔分期付款业务的所有剩余未还部分将于发生上述情况之时视为全部到期，' + '持卡人应当一次性偿还全部剩余分期付款的本金、手续费及相关费用。</p>' + '<p>13、发卡机构修改本业务条款及细则，应通过官方网站或其他相关渠道公告的方式事先通知持卡人,' + '并于法定公告期满后生效，敬请持卡人留意。</p>' + '<p>14、此业务条款之未尽事宜，依据《安徽农村商业银行系统杜鹃信用卡章程》、《安徽农村商业银行系统杜鹃信用卡领用合约》办理。</p>' + '<p>15、安徽农村商业银行系统杜鹃信用卡发卡机构拥有对此业务条款和条件的最终解释权。</p></div>';
