<template>
    <div class="public-index">
        <apply ref="child" :dataCopy="infoData" :mapCopy="map"></apply>
        <div class="field-action">
            <mt-button type="primary" size="large" @click="confirm">确认申请</mt-button>
        </div>
    </div>
</template>

<script>
    import Apply from '../public/apply';
    export default {
        name: 'public-index',
        components: {
            Apply
        },
        data() {
            return {
                infoData: {},
                map: [
                    { key: 'account', title: '信用卡卡号' },
                    { key: 'OUTCARDNO', title: '借记卡卡号' },
                    { key: 'MPAMT', title: '申请金额', type: 'amount-unit', style: { color: '#FF942F' } },
                    { key: 'num', title: '分期期数' },
                    { key: 'INSTALAMT', title: '每期还款额', type: 'amount-unit', style: { color: '#FF942F' } },
                    { key: 'FEEAMT', title: '分期手续费', type: 'amount-unit', style: { color: '#FF942F' } }
                ]
            };
        },
        created() {
            let ls = this.$ls.getItem('credit-withdrawals');
            this.infoData = Object.assign({}, ls);
        },
        methods: {
            confirm() {
                // this.$router.push({ name: 'CreditHuiResult' });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .field-action {
        clear: both;
        margin-top: 116px;
    }
</style>


