<template>
    <div class="credit-repayment-pay">
        <cell-map :dataCopy="infoData" :map="map"></cell-map>
        <div class="top">
            请输入借记卡交易密码
        </div>
        <div class="field-part">
            <cell-password :options=" cellPassword "></cell-password>
        </div>

        <div class="field-action self">
            <slot name="btn"></slot>
        </div>

    </div>
</template>

<script>
    export default {
        name: 'credit-repayment-pay',
        props: ['dataCopy', 'mapCopy'],
        data() {
            return {
                cellPassword: {
                    title: '交易密码',
                    rule: 'number' // number or mix
                }
            };
        },
        computed: {
            infoData() {
                return this.dataCopy;
            },
            map() {
                return this.mapCopy;
            }
        }
    };

</script>

<style lang="scss" scoped>
    .top {
        padding: 40px 50px 20px;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #666666;
        line-height: 36px;
    }

    .self {
        margin-top: 80px;
    }
</style>


