import ajax from '@/services/ajax';
import api from '@/services/api';

export function getBankList(data) {
    return ajax.post( api.getBankList, data );
}

export function getPayeeList(data) {
    return ajax.post( api.getPayeeList, data );
}

export function getBankCityList(data) {
    return ajax.post( api.getBankCityList, data );
}

export function getBankBranchList(data) {
    return ajax.post( api.getBankBranchList, data );
}

export function getAccountList(data) {
    return ajax.post( api.getPaymentAccountList, data, api.getPaymentAccountList );
}









