<template>
	<div class="credit-manage-pwd">
		<div class="cell-part">
			<mt-cell @click.native="toOperate('CreditManagePwdSearchOperate')" title="查询密码" is-link></mt-cell>
			<mt-cell @click.native="toOperate('CreditManagePwdTransactionOperate')" title="交易密码" is-link></mt-cell>
		</div>
	</div>
</template>

<script>
	
export default {
	methods: {
		toOperate(name) {
			this.$router.push({name: name});
		}
	}
};

</script>


