<template>
    <div class="public-list">
        <div class="field-top">
            选择要分期的交易
        </div>

        <template v-for="item in listData">
            <cell title="bill.TRANTYPE" @click.native="doHref(item)">
                <div class="title" slot="title">{{item.TRANTYPE}}</div>
                <div class="label" slot="label">{{item.PURDATE1}}</div>
                <div class="value" slot="value">{{item.AMOUNT | formatNumber(2)}}元</div>
            </cell>
        </template>
    </div>
</template>

<script>
    export default {
        name: 'public-list',
        props: ['dataCopy'],
        computed: {
            listData() {
                return this.dataCopy;
            }
        },
        methods: {
            doHref(item) {
                this.$emit('emit-href', item);
            }
        }
    };

</script>

<style lang="scss" scoped>
    .field-top {
        padding: 20px 50px;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #666666;
        line-height: 36px;
    }

    .title {
        font-family: PingFangSC-Regular;
        font-size: 16px;
        color: #272727;
        letter-spacing: 0;
        line-height: 44px;
    }

    .label {
        margin-top: 10px;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #B5B5B5;
        line-height: 36px
    }

    .value {
        font-family: SFUIText-Regular;
        font-size: 17px;
        color: #272727;
        line-height: 48px;
    }
</style>


