<template>
	<div class="credit-manage-card-set-cost">
		<div class="cell-part">
			<mt-cell title="信用卡卡号" :value="card"></mt-cell>
		</div>
		<div class="cell-part">
        	<mt-cell title="消费凭密" @click.native=" showSelectorCost()  "is-link :value=" costType || '请选择' "></mt-cell>
		</div>
		<div class="cell-part">
			<cell-password :options=" cellPassword "></cell-password>
		</div>
		<div class="field-warn text-right">
        	<a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
        </div>
		<div class="button-bottom">
			<mt-button @click.native="submit" type="primary" size="large">保存</mt-button>
		</div>
		<selector-action ref="selectorCost" :options=" selectorCost "></selector-action>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			card: '',
			costType: '',
			cellPassword: {
                title: '查询密码：',
                rule: 'number' // number or mix
            },
            selectorCost: {
                title: '请选择消费凭密',
                key: 'field1',
                value: 'field2',
                checked: '',
                actionList: [
                    {
                        field1: 1,
                        field2: '签名+交易密码'
                    },
                    {
                        field1: 2,
                        field2: '仅签名'
                    }
                ]
            }
		};
	},
	watch: {
		rename: function(val){
			if(val) this.submitStatus = true;
			else this.submitStatus = false;
		}
	},
	methods: {
		toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
		submit() {
			this.setCreditCost();
		},
		setCreditCost() {
			service.setCreditCost()
			.then((data) => {
				this.$router.push({name: 'CreditManageCardSetSuccess', params: {type: 'cost'}});
			}, (err) => {
				console.log(err);
			});
		},
		showSelectorCost() {
            this.$refs.selectorCost.show().then((data) => {
                this.selectorCost.checked = data.item.field1;
                this.costType = data.item.field2;
                this.costTypeId = data.item.field1;
            }, (data) => {
            });
        }
	},
	created() {
		this.card = this.$route.params.card;
	}
};

</script>

<style lang="scss">
	
.credit-manage-card-set-cost{
	.forget-pwd{
		color: #308CF8;
		font-size: 13px;
	}
}

</style>


