<template>
	<div class="more-service-quick-loss-success">
		<tip-page tipTitle="挂失成功" tipMsg="如需解挂请持本人身份证件到安徽农金任一营业网点办理；如需补卡请使用闪电补卡功能">
			<div class="card-contain">
				<div class="card-msg">
					<div class="line1">
						<div class="title">信用卡卡号</div>
						<div class="value number">{{detail.ACCOUNT}}</div>
					</div>
					<div class="line2">
						<div class="title">卡片当前状态</div>
						<div class="value status">{{detail.STATUS}}</div>
					</div>
					<div class="line3">
						<div class="title">卡片挂失费用</div>
						<div class="value number">{{detail.FEE}}元</div>
					</div>
				</div>
				<div class="button-bottom">
					<mt-button @click.native="toPatchCard" type="primary" size="large" class="set-mbottom">闪电补卡</mt-button>
					<mt-button @click.native="back" type="default" size="large">返回信用卡首页</mt-button>
				</div>
			</div>
		</tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			detail: {}
		};
	},
	created(){
	},
	methods: {
		toPatchCard() {
			this.$router.push({name: 'PatchCard', params: {ACCOUNT: '6229538100106373333'}});
		},
		back() {
			this.$router.push({name: 'Credit'});
		}
	}
};

</script>

<style lang="scss">
.more-service-quick-loss-success{
	.card-contain{
		margin: 30px 40px 80px;
		.card-msg{
			padding: 40px 50px;
			height: 246px;
			font-size: 15px;
			color: #666666;
			border-radius: 8px;
			background-color: #fff;
			@include image-set('../../../../assets/img/credit/card');
			background-size: auto 100%;
			box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.06);
			> div{
				height: 42px;
				line-height: 42px;
				.title{
					float: left;
				}
				.value{
					float: right;
				}
				.number{
					color: #272727;
				}
				.status{
					color: #FF511A;
				}
			}
			.line2{
				margin: 20px 0;
			}
			> div:after{
				content: "clear"; 
				display: block; 
				height: 0; 
				clear: both; 
				overflow: hidden; 
				visibility: hidden;
			}
		}
		.set-mbottom{
			margin-bottom: 30px;
		}
	}
}
</style>


