<template>
    <div class="bill">
        <div class="field-part">
            <mt-cell class="allow-down" title="信用卡卡号" @click.native="showCreditAccount()" v-model="formData.PAYER_ACCOUNT" is-link></mt-cell>
            <selector-payment-account ref="creditAccount" :options=" creditAccount "></selector-payment-account>
        </div>

        <div class="field-part">
            <div class="panel">
                <div class="panel-top">
                    <p class="title">剩余未还（元）</p>
                    <p class="amount">{{infoData.residue_refund_amount | formatNumber(2)}}</p>
                    <p class="late-day">最终还款日：{{infoData.refund_date}}</p>
                </div>
                <div class="panel-bottom">
                    <p class="now">本期应还：{{infoData.current_refund_amount | formatNumber(2)}}元</p>
                    <p class="low">最低还款：{{infoData.low_refund | formatNumber(2)}}元</p>
                </div>
            </div>
        </div>

        <div class="field-part">
            <account-select title='借记卡卡号' :value='formData.PAYEE_ACCOUNT' :balance="formData.PAYEE_ACCOUNT_BALANCE" @click.native="showDebitAccount()"></account-select>
            <selector-payment-account ref="debitAccount" :options=" debitAccount "></selector-payment-account>
            <cell-transfer-money title="还款金额" placeholder="请输入还款金额" @focus="amountFocus()" @blur="amountBlur()" validate="还款金额大于借记卡金额"
                :totalMoney="debitAccount.checked.balance" v-model="formData.AMOUNT">
            </cell-transfer-money>

            <div v-if="showAmount">
                <div class="cover"></div>
                <ul class="amount-wrap">
                    <li class="item" @click.native="setAmount('residue_refund_amount')">
                        <span class="title">全额还款</span>
                        <span class="value">{{infoData.residue_refund_amount | formatNumber(2)}}元</span>
                    </li>
                    <li class="item" @click="setAmount('low_refund')">
                        <span class="title">最低还款</span>
                        <span class="value">{{infoData.low_refund | formatNumber(2)}}元</span>
                    </li>
                </ul>
            </div>
        </div>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="next">下一步</mt-button>
        </div>
        <div class="warn">
            手头有点紧?可使用惠分单分期哦
        </div>

        <div class="field-action">
            <mt-button type="primary" size="large" @click="test">为他人还款</mt-button>
        </div>
    </div>
</template>

<script>
    import * as servicesC from '@/services/credit/credit';
    import * as services from '@/services/bank/selector';
    export default {
        name: 'bill',
        data() {
            return {
                formData: {
                    PAYER_ACCOUNT: '',
                    ACCT_NAME: '',
                    ACCT_TYPE: '',
                    PAYEE_ACCOUNT: '',
                    COR_NAME: '',
                    INCARDTYPE: '',
                    PAYEE_ACCOUNT_BALANCE: '',
                    AMOUNT: ''
                },
                infoData: {},
                creditAccount: {
                    title: '选择信用卡',
                    type: 'credit',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                },
                debitAccount: {
                    title: '选择借记卡',
                    type: 'payment',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                },
                showAmount: false
            };
        },
        created() {
            let ls = this.$ls.getItem('credit-repayment');
            if (ls != null) {
                this.formData.AMOUNT = ls.AMOUNT;
            }
            servicesC.queryCreditCard()
                .then((res) => {
                    this.creditAccount.accountList = res.data;
                    if (ls == null) {
                        let _data = res.data.filter(item => {
                            return item.ISDEFAULT === true;
                        })[0] || {};
                        this.creditAccount.checked = _data;
                        this.formData.PAYER_ACCOUNT = _data.ACCOUNT;
                        this.formData.ACCT_NAME = _data.NAME;
                        this.formData.ACCT_TYPE = _data.TYPE;
                    } else {
                        let _data = res.data.filter(item => {
                            return item.ACCOUNT === ls.PAYER_ACCOUNT;
                        })[0] || {};
                        this.creditAccount.checked = _data;
                        this.formData.PAYER_ACCOUNT = ls.PAYER_ACCOUNT;
                        this.formData.ACCT_NAME = ls.ACCT_NAME;
                        this.formData.ACCT_TYPE = ls.ACCT_TYPE;
                    }
                    let params = {
                        account: this.formData.PAYER_ACCOUNT
                    };
                    this.getCreditInfo(params)
                        .then(res => {
                            this.infoData = res;
                        }, err => {
                            console.log(err);
                        });
                }, (err) => {
                    console.log(err);
                });
            services.getAccountList()
                .then((res) => {
                    this.debitAccount.accountList = res.data;
                    if (ls == null) {
                        let _data = res.data.filter(item => {
                            return item.isDefault === true;
                        })[0] || {};
                        this.debitAccount.checked = _data;
                        this.formData.PAYEE_ACCOUNT = _data.ACCOUNT;
                        this.formData.COR_NAME = _data.name;
                        this.formData.INCARDTYPE = _data.TYPE;
                        this.formData.PAYEE_ACCOUNT_BALANCE = _data.balance;
                    } else {
                        let _data = res.data.filter(item => {
                            return item.ACCOUNT === ls.PAYEE_ACCOUNT;
                        })[0] || {};
                        this.debitAccount.checked = _data;
                        this.formData.PAYEE_ACCOUNT = ls.PAYEE_ACCOUNT;
                        this.formData.COR_NAME = ls.COR_NAME;
                        this.formData.INCARDTYPE = ls.INCARDTYPE;
                        this.formData.PAYEE_ACCOUNT_BALANCE = ls.PAYEE_ACCOUNT_BALANCE;
                    }
                }, (err) => {
                    console.log(err);
                });
        },
        methods: {
            showCreditAccount() {
                this.$refs.creditAccount.show().then((data) => {
                    this.creditAccount.checked = data.item;
                    this.formData.PAYER_ACCOUNT = this.creditAccount.checked.ACCOUNT;
                    this.formData.ACCT_NAME = this.creditAccount.checked.NAME;
                    this.formData.ACCT_TYPE = this.creditAccount.checked.TYPE;
                    this.getCreditInfo()
                        .then(res => {
                            this.infoData = res;
                        }, err => {
                            console.log(err);
                        });
                }, (data) => {
                });
            },
            showDebitAccount() {
                this.$refs.debitAccount.show().then((data) => {
                    this.debitAccount.checked = data.item;
                    this.formData.PAYEE_ACCOUNT = this.debitAccount.checked.ACCOUNT;
                    this.formData.COR_NAME = this.debitAccount.checked.name;
                    this.formData.INCARDTYPE = this.debitAccount.checked.TYPE;
                    this.formData.PAYEE_ACCOUNT_BALANCE = this.debitAccount.checked.balance;
                }, (data) => {
                });
            },
            getCreditInfo(params) {
                let self = this;
                return new Promise((resolve, reject) => {
                    servicesC.queryCreditList()
                        .then(res => {
                            let _data = res.data.filter(item => {
                                return item.account === self.formData.PAYER_ACCOUNT;
                            })[0] || {};
                            resolve(_data);
                        }, (err) => {
                            reject(err);
                        });
                }, err => {

                });
            },
            amountFocus() {
                this.showAmount = true;
            },
            setAmount(key) {
                console.log(key);
                this.formData.amount = this.infoData['key'];
            },
            amountBlur() {
                this.showAmount = false;
            },
            next() {
                // if (this.$validate.isEmpty(_data.amount)) {
                //     this.$msg('请输入还款金额');
                //     return false;
                // }
                // if (!this.$validate.isAmount(_data.amount)) {
                //     this.$msg('金额格式错误');
                //     return false;
                // }
                this.$router.push({ name: 'CreditRepaymentPay' });
            },
            test() {
                this.$router.push({ name: 'CreditRepaymentOther' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName']) {
                this.$ls.setItem('credit-repayment', this.formData);
            } else {
                this.$ls.removeItem('credit-repayment', this.formData);
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .panel {
        text-align: center;
        padding: 30px 0 0;
        background-color: #fff;

        .panel-top {
            position: relative;
            padding-bottom: 30px;
            margin: 0 50px;
            @include mini-border(bottom, #E4E5E6, after);

            .title {
                font-family: PingFangSC-Regular;
                font-size: 13px;
                color: #999999;
                line-height: 36px;
            }
            .amount {
                margin: 10px 0;
                font-family: SFUIDisplay-Regular;
                font-size: 30px;
                color: #272727;
                line-height: 72px;
            }

            .late-day {
                font-family: PingFangSC-Regular;
                font-size: 13px;
                color: #999999;
                line-height: 36px;
            }
        }

        .panel-bottom {
            padding: 31px 68px 30px 60px;

            p {
                display: inline-block;
                opacity: 0.6;
                font-family: PingFangSC-Regular;
                font-size: 13px;
                color: #272727;
                letter-spacing: 0;
            }

            .low {
                float: right;
            }
        }
    }

    .amount-wrap {
        background-color: #fff;

        .item+.item {
            @include mini-border(top, #E4E5E6, before);
        }

        .item {
            position: relative;
            height: 100px;
            margin-left: 100px;

            .title {
                font-family: PingFangSC-Regular;
                font-size: 15px;
                color: #272727;
                line-height: 100px;
            }

            .value {
                margin-left: 30px;
                font-family: PingFangSC-Regular;
                font-size: 15px;
                color: #308CF7;
                line-height: 100px;
            }
        }
    }

    .field-action {
        margin-top: 80px;
    }

    .warn {
        text-align: center;
        font-family: PingFangSC-Regular;
        font-size: 13px;
        color: #308CF8;
        line-height: 36px;
    }
</style>


