<template>
	<div class="auto-repay-open">
        <div :class="{'cell-part': !isOpen}">
            <mt-cell class="auto-switch" title="自动还款">
                <mt-switch v-model="isOpen"></mt-switch>
            </mt-cell>
        </div>
		<template v-if="isOpen">
            <div class="cell-part">
    			<mt-cell title="原自动还款账号" :value="detail.REPAYACCT"></mt-cell>
    			<mt-cell title="原自动还款方式" :value="detail.REPAYNAME"></mt-cell>
            </div>
			
            <div class="cell-part">
                <div class="head-hint">
                    您可重新调整自动还款设置
                </div>
    			<mt-cell class="allow-down" @click.native="showSelectorPaymentAccount()" title="新自动还款账号" placeholder="" :value="tdetail.PAYEE_ACCOUNT || '请选择'" is-link>
            	</mt-cell>
            	<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
                <mt-cell class="allow-down" title="新自动还款方式" @click.native=" showSelectorAction()  "is-link :value=" tdetail.REPAT_TYPE || '请选择' ">
                </mt-cell>
            </div>
            <template v-if="!forbid">
    	        <div class="cell-part">
    		        <cell-password :options=" cellPassword "></cell-password>
                </div>
	            <div class="cell-hint text-right">
	            	<a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
	            </div>
	            <div class="button-bottom">
	            	<mt-button @click.native="submit" type="primary" size="large">确认修改</mt-button>
	            </div>
            </template>
		</template>
		<template v-if="!isOpen">
            <div class="cell-part">
			 <cell-password :options=" cellPassword "></cell-password>
            </div>
            <div class="cell-hint text-right">
            	<a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
            </div>
            <div class="button-bottom">
            	<mt-button @click.native="closeSubmit" type="primary" size="large">确认关闭</mt-button>
            </div>
		</template>
        <selector-action ref="selectorAction" :options=" selectorAction "></selector-action>
	</div>
</template>

<script>

import service from '@/services/credit/manage';
import * as commService from '@/services/bank/selector';

export default {
	data() {
		return {
			isOpen: true,
            detail: {
    			REPAYACCT: '',
    			REPAYCODE: '',
                REPAYNAME: ''
            },
            tdetail: {
                PAYEE_ACCOUNT: '',
                REPAT_TYPE: '',
                REPAT_TYPE_ID: ''
			},
			forbid: true,
			selectorPaymentAccount: {
                title: '请选择新还款账号',
                checked: {},
                showAddBtn: true,
                showWarn: false,
                accountList: []
            },
            cellPassword: {
                title: '查询密码',
                rule: 'number' // number or mix
            },
            selectorAction: {
                title: '请选择新自动还款方式',
                key: 'field1',
                value: 'field2',
                checked: '',
                actionList: [
                    {
                        field1: 1,
                        field2: '全款还款'
                    },
                    {
                        field1: 2,
                        field2: '部分还款'
                    }
                ]
            }
		};
	},
	watch: {
		tdetail: {
			handler: function(item){
                this.forbid = !(item.PAYEE_ACCOUNT && item.REPAT_TYPE);
			},
			deep: true
		}
	},
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.tdetail.PAYEE_ACCOUNT = data.item.ACCOUNT;
            }, data => {
            });
        },
        toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
        submit() {
        	service.setAutoRepay({
                OPTION: '2',
                PAYEE_ACCOUNT: this.tdetail.PAYEE_ACCOUNT,
                REPAT_TYPE: this.tdetail.REPAT_TYPE_ID,
                PAY_PWD: ''
            })
        	.then((data) => {
        		this.$router.push({name: 'CreditManageAutoRepaySuccess', params: {type: '修改'}});
        	}, (err) => {
        		console.log(err);
        	});
        },
        closeSubmit() {
        	service.setAutoRepay({
                OPTION: '4',
                PAYEE_ACCOUNT: this.detail.PAYEE_ACCOUNT,
                REPAT_TYPE: this.detail.REPAYCODE,
                PAY_PWD: ''
            })
        	.then((data) => {
        		this.$router.push({name: 'CreditManageAutoRepaySuccess', params: {type: '关闭'}});
        	}, (err) => {
        		console.log(err);
        	});
        },
        showSelectorAction() {
            this.$refs.selectorAction.show().then((data) => {
                this.selectorAction.checked = data.item.field1;
                this.tdetail.REPAT_TYPE = data.item.field2;
                this.tdetail.REPAT_TYPE_ID = data.item.field1;
            }, (data) => {
            });
        }
	},
	created() {
        commService.getAccountList().then((res) => {
            this.selectorPaymentAccount.accountList = res.data;
        }, (err) => {
            console.log( err );
        });
	}
};

</script>

<style lang="scss">

.auto-repay-open{
    .auto-switch:after{
        left: 0;
    }
    .auto-switch:last-child{
        background-image: none;
    }
	.mint-cell-title,{
		width: 3.6rem;
	}
	.mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
    .forget-pwd{
		color: #308cf8;
		font-size: 13px;
	}
}

</style>


