<template>
    <div class="cash-advance-apply">
        <cell-map :dataCopy="infoData" :map="map"></cell-map>
        <div class="field-title">
            <p>请输入信用卡交易密码</p>
        </div>

        <div class="field-part">
            <cell-password :options=" cellPassword "></cell-password>
        </div>

        <div class="forget-pwd">
            <p>忘记查询密码</p>
        </div>

        <div class="field-btn">
            <mt-button size="large" type="primary" @click="apply()">确认申请</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/credit/credit';
    export default {
        name: 'cash-advance-apply',
        data() {
            return {
                infoData: {},
                map: [
                    { key: 'PAYER_ACCOUNT', title: '信用卡卡号' },
                    { key: 'PAYEE_ACCOUNT', title: '借记卡卡号' },
                    { key: 'AMOUNT', title: '转出金额', type: 'amount-unit' }
                ],
                cellPassword: {
                    title: '交易密码',
                    rule: 'number' // number or mix
                }
            };
        },
        created() {
            let ls = this.$ls.getItem('credit-cash-advance') || {};
            this.infoData = ls;
        },
        methods: {
            apply() {
                services.applyCashAdvance()
                    .then(res => {
                        console.log(res);
                    }, err => {
                        console.log(err);
                    });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName']) {
                this.$ls.removeItem('credit-cash-advance');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .field-title {
        margin: 40px 0 20px 50px;

        p {
            font-size: 13px;
            color: #666666;
            line-height: 36px;
        }
    }

    .forget-pwd {
        margin: 20px 50px 0;
        clear: both;

        p {
            float: right;
            font-family: PingFangSC-Regular;
            font-size: 13px;
            color: #308CF8;
            line-height: 36px;
        }
    }

    .field-btn {
        clear: both;
        margin-top: 80px;
    }
</style>


