<template>
	<div class="card-set-success">
		<tip-page :tipTitle="msg" tipButton="返回信用卡管理" @submit="back">
		</tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {};
	},
	computed: {
		msg: function(){
			let type = this.$route.params.type;
			if(type === 'rename'){
				return '别名修改成功';
			}else if(type === 'cost'){
				return '消费凭密修改成功';
			}else{
				return '卡片删除成功';
			}
		}
	},
	methods: {
		back() {
			this.$router.push({name: 'CreditManage'});
		}
	}
};

</script>

<style lang="scss">
.card-set-success{
}
</style>


