<template>
    <div class="borrow-money-submit">
        <submit ref="child" ls="credit-borrow-money" :dataCopy="infoData" :mapCopy="map">
            <div class="field-part" slot="MPAMT">
                <cell-transfer-money title="申请金额" validate="可用额度不足" :totalMoney="this.infoData.AVLIMT" placeholder="起点金额为5000元，且需为1000的整数倍"
                    v-model="formData.MPAMT">
                </cell-transfer-money>
            </div>

            <div class="field-part" slot="account">
                <mt-cell title="借记卡卡号" @click.native="showOutAccount()" v-model="formData.OUTCARDNO" class="allow-down" is-link></mt-cell>
                <selector-payment-account ref="outAccount" :options=" outAccount "></selector-payment-account>
            </div>
        </submit>

        <div class="agree">
            <template>
                <input type="checkbox" id="check" class="box" v-model="agree">
                <label for="check" class="label"></label>
            </template>
            <span class="promise">我已阅读并同意</span>
            <span class="item" @click="showAgreement">惠借钱分期协议</span>
        </div>

        <div class="field-action" slot="btn">
            <mt-button :disabled="!clickable || !agree" type="primary" size="large" @click="submit">提交申请</mt-button>
        </div>

        <dialog-agreement ref="agreement" :options="agreeOptions"></dialog-agreement>
    </div>
</template>

<script>
    import * as services from '@/services/credit/credit';
    import * as servicesO from '@/services/public';
    import Submit from '../public/submit';
    import Html from '../consume/agreement';
    export default {
        name: 'borrow-money-submit',
        components: {
            Submit
        },
        data() {
            return {
                infoData: {},
                map: [
                    { key: 'account', title: '信用卡卡号' },
                    { key: 'AVLIMT', title: '惠借钱可用额度', type: 'amount-unit', style: { color: '#FF942F' } }
                ],
                agree: false,
                clickable: false,
                formData: {
                    MPAMT: '',  // 申请金额
                    OUTCARDNO: '',  // 转出账号
                    OUTBANKNAME: '安徽农金'
                },
                agreeOptions: {
                    title: '惠借钱分期协议',
                    btnConfirmText: '同意协议并提交'
                },
                outAccount: {
                    title: '选择付款账号',
                    type: 'credit',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                }
            };
        },
        created() {
            let ls = this.$ls.getItem('credit-borrow-money');
            this.infoData = ls;
            this.formData.MPAMT = ls.MPAMT || '';
            servicesO.getCommonAccountList()
                .then(res => {
                    this.outAccount.accountList = res.LIST;
                    let _data;
                    if (ls.OUTCARDNO == null) {
                        _data = res.LIST.filter(item => {
                            return item.IS_DEFAULT === '1';
                        })[0] || {};
                    } else {
                        _data = res.LIST.filter(item => {
                            return item.ACCOUNT === ls.OUTCARDNO;
                        })[0] || {};
                    }
                    this.outAccount.checked = _data;
                    this.formData.OUTCARDNO = this.outAccount.checked.ACCOUNT;
                }, err => {
                    console.log(err);
                });
        },
        watch: {
            'formData.MPAMT': {
                handler: 'validate',
                deep: true
            }
        },
        methods: {
            showAgreement() {
                this.$refs.agreement.show(Html)
                    .then(data => {
                        this.agree = true;
                    }, data => {
                        console.log(data);
                    });
            },
            showOutAccount() {
                this.$refs.outAccount.show().then((data) => {
                    this.outAccount.checked = data.item;
                    this.formData.OUTCARDNO = this.outAccount.checked.ACCOUNT;
                }, (data) => {
                });
            },
            validate(val) {
                if (this.$validate.isEmpty(val)) {
                    this.clickable = false;
                    return false;
                }
                this.clickable = true;
            },
            submit() {
                let params = {
                    CARDNO: '',
                    OPTION: 4,
                    MPAMT: '',
                    NBRMTHS: ''
                };
                services.queryOrderStagesApply(params)
                    .then(res => {
                        let ls = this.$ls.getItem('credit-borrow-money') || {};
                        let obj = Object.assign({}, ls, this.$refs.child.dataForm, this.formData, res.data);
                        this.$ls.setItem('credit-borrow-money', obj);
                        this.$router.push({ name: 'CreditBorrowMoneyApply' });
                    }, err => {
                        console.log(err);
                    });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName']) {
                this.$ls.removeItem('credit-borrow-money');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .agree {
        margin: 40px 0 0 50px;

        .box+.label {
            position: relative;
            display: inline-block;
            margin-right: 20px;
            height: 39px;
            width: 39px;
            border: 1px solid #559BDF;
            border-radius: 2px;
            vertical-align: middle;
        }
        /**
         * 隐藏默认的checkbox
         */
        .box {
            display: none;
        }

        .box:checked+label:after {
            content: '\2714';
            position: absolute;
            top: 0px;
            left: 0px;
            color: #559BDF;
            width: 100%;
            text-align: center;
            font-size: 1.4em;
            vertical-align: text-top;
        }

        span {
            font-size: 13px;
            color: #666666;
            line-height: 36px;
        }

        .item {
            color: #559BDF;
        }
    }

    .field-action {
        margin-top: 84px;
    }
</style>


