<template>
	<div class="auto-repay-success">
		<tip-page :tipTitle="tipTitle" :tipMsg="tipMsg" tipButton="返回信用卡管理" @submit="back">
		</tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			tipTitle: '',
			tipMsg: ''
		};
	},
	created() {
		let type = this.$route.params.type;
		this.tipTitle = `${type}申请提交成功`;
		this.tipMsg = `自动还款的${type}将于次日生效`;
	},
	methods: {
		back() {
			this.$router.push({name: 'CreditManage'});
		}
	}
};

</script>


