<script>
	import { mSwiper } from 'vue-m-swipers';
	import * as bservice from '@/services/public';

	export default {
		components: {
			mSwiper
		},
		data() {
			return {
				status: '',
				account: '',
				detail: {},
				selectorPaymentAccount: {
					title: '选择账户',
					type: 'payment',
					checked: {},
					showAddBtn: false,
					showWarn: false,
					showBankName: false,
					accountList: []
				},
				swiperAuto: 4000,
				swiperData: [
					{
						img: 'static/img/temp/img-credit-banner.png'
					},
					{
						img: 'static/img/temp/img-credit-banner.png'
					},
					{
						img: 'static/img/temp/img-credit-banner.png'
					},
					{
						img: 'static/img/temp/img-credit-banner.png'
					},
					{
						img: 'static/img/temp/img-credit-banner.png'
					}
				]
			};
		},
		created() {
			this.getCreditList();
			this.getBannerImage();
		},
		methods: {
			bill() {
				this.$router.push({ name: 'CreditBill' });
			},
			repayment() {
				this.$router.push({ name: 'CreditRepayment' });
			},
			huiService(name) {
				this.$router.push({ name: name });
			},
			moreService(name) {
				this.$router.push({ name: name });
			},
			swiperClick(index) {
				alert(index);
			},
			getCreditList(type) {
	            bservice.getCreditList()
	            .then( data => {
	                this.selectorPaymentAccount.accountList = data.LIST;
	                let defaultAccount = data.LIST[0];
	                this.selectorPaymentAccount.checked = defaultAccount;
					this.account = defaultAccount.ACCOUNT;
					this.detail = defaultAccount;
					console.log(this.detail);
	                if(type){
	                    this.paymentAccount();
	                }
	            }, err => {
	                console.log( err );
	            });
	        },
	        showSelectorPaymentAccount() {
	            if(this.selectorPaymentAccount.accountList.length == 0){
	                this.getCreditList(true);
	            } else{
	                this.paymentAccount();
	            }
	        },
	        paymentAccount() {
	            this.$refs.selectorPaymentAccount.show().then(data => {
	                this.selectorPaymentAccount.checked = data.item;
	                this.detail = data.item;
	                console.log(this.detail);
	                this.account = data.item.ACCOUNT;
	            }, data => {
	            });
	        },
			getBannerImage() {
				bservice.getBannerImage({
					PICTYPE: '2'
				}).then( data => {
					// console.log(data);
				}, err => {
					console.log(err);
				});
			}
		}
	};

</script>

