<template>
    <div class="consume">
        <index ref="child" ls="credit-order-stages">
            <div class="amount" slot="amount">
                <p class="p">可分期金额（元）</p>
                <p class="num">{{AMOUNT | formatNumber(2)}}</p>
            </div>

            <template slot="info">
                <p class="info">
                    您可对整个账单中的消费金额进行分期，并在约定期限内偿还。
                </p>
            </template>

            <div class="field-action" slot="btn">
                <mt-button type="primary" size="large" @click="doApply">我要申请</mt-button>
            </div>

            <template slot="process">
                <p class="title">
                    办理流程：
                </p>
                <p>
                    刷卡消费>>>持卡人申请(账单生成之前)>>>分期偿还
                </p>
            </template>
            
            <template slot="warn">
                <p class="title">
                    温馨提示：
                </p>
                <p>
                    1. 公务卡不支持该交易；
                </p>
                <p>
                    2. 惠分单可分期金额最高为账单金额的90%，剩余部分当期仍需还款；最低可分期金额为1000元；
                </p>
                <p>
                    3. 您可选择3、6、9、12、18、24期进行惠分单分期，一个月为一期；
                </p>
                <p>
                    4. 惠分单分期申请时间为账单日后至最后还款日前两天，请在时间范围内操作；
                </p>
                <p>
                    5. 惠分单分期撤销仅限当日处理。
                </p>
            </template>
        </index>
    </div>
</template>

<script>
    import * as services from '@/services/credit/credit';
    import Index from '../public';
    export default {
        name: 'consume',
        components: {
            Index
        },
        data() {
            return {
                AMOUNT: ''
            };
        },
        mounted() {
            let params = {
                CARDNO: this.$refs.child.formData.account,
                OPTION: 3
            };
            services.queryOrderStagesApply(params)
                .then(res => {
                    this.AMOUNT = res.data.REMSTMAMT;
                }, err => {
                    console.log(err);
                });
        },
        methods: {
            doApply() {
                this.$router.push({ name: 'CreditOrderStagesSubmit' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName']) {
                let obj = Object.assign({}, this.$refs.child.formData, { AMOUNT: this.AMOUNT });
                this.$ls.setItem('credit-order-stages', obj);
            } else {
                this.$ls.removeItem('credit-order-stages');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .p+.num {
        margin-top: 12px;
    }
</style>


