<template>
    <div class="public-submit">
        <div class="field-part">
            <cell-map :dataCopy="infoData" :map="map"></cell-map>
        </div>

        <slot name="MPAMT"></slot>

        <div class="field-part">
            <mt-cell title="分期期数" class="allow-down" :value="dataForm.num" @click.native="showSelect" is-link>
            </mt-cell>

            <selector-action ref="selectorAction" :options=" selectorAction "></selector-action>

        </div>

        <slot name="account"></slot>

    </div>
</template>

<script>
    export default {
        name: 'public-submit',
        props: ['dataCopy', 'mapCopy', 'ls'],
        data() {
            return {
                dataForm: {
                    num: '',
                    numId: '',
                    MPAMT: ''
                },
                selectorAction: {
                    title: '选择分期期数',
                    key: 'field1',
                    value: 'field2',
                    checked: '',
                    actionList: [
                        { field1: 3, field2: '3期' },
                        { field1: 6, field2: '6期' },
                        { field1: 9, field2: '9期' },
                        { field1: 12, field2: '12期' },
                        { field1: 18, field2: '18期' }
                    ]
                }
            };
        },
        computed: {
            infoData() {
                return this.dataCopy;
            },
            map() {
                return this.mapCopy;
            }
        },
        created() {
            let ls = this.$ls.getItem(this.ls);
            if (ls.numId == null) {
                this.selectorAction.checked = this.selectorAction.actionList[0];
            } else {
                let _data = this.selectorAction.actionList.filter(item => {
                    return item.field1 === ls.numId;
                })[0];
                this.selectorAction.checked = _data;
            }
            this.dataForm.num = this.selectorAction.checked.field2;
            this.dataForm.numId = this.selectorAction.checked.field1;
        },
        methods: {
            showSelect() {
                this.$refs.selectorAction.show().then((data) => {
                    this.selectorAction.checked = data.item.field1;
                    this.dataForm.num = data.item.field2;
                    this.dataForm.numId = data.item.field1;
                }, (data) => {
                });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .transfer-amount {
        font-size: 16px;
        color: #FF942F;
        line-height: 50px;
    }
</style>


