<template>
    <div class="bill">
        <div v-if="!code">
            <div class="hd">
                <div v-if="totalAmount" class="total-amount">
                    <p class="amount-title">账单总额（元）</p>
                    <p class="amount">{{totalAmount | formatNumber(2)}}</p>
                </div>

                <div v-if="billDate || refundDate" class="day-group">
                    <div class="left">
                        账单日：{{billDate}}
                    </div>
                    <div class="right">
                        还款日：{{refundDate}}
                    </div>
                </div>
            </div>

            <div class="field-part">
                <cell class="cell" v-for="(bill, index) in billList" key="index" :title="bill.DESLINE1">
                    <div class="title" slot="title">{{bill.DESLINE1}}</div>
                    <div class="cell-label" slot="label">尾号{{bill.CARDEND}}</div>
                    <div class="value" slot="value">{{bill.AMOUNT}}元</div>
                    <div class="cell-desc" slot="desc">{{bill.PURDATE1}}</div>
                </cell>
            </div>

            <slot name="btn"></slot>
        </div>

        <div class="no-data" v-else>
            <p>未查询到明细记录</p>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/credit/credit';
    export default {
        name: 'bill',
        props: ['option'],
        computed: {
            code() {
                return this.option.code;
            },
            billDate() {
                return this.option.bill_date || false;
            },
            refundDate() {
                return this.option.refund_date || false;
            },
            totalAmount() {
                return this.option.total_amount || false;
            },
            billList() {
                return this.option.list;
            }
        }
    };

</script>

<style lang="scss" scoped>
    .hd {
        background-color: #fff;

        .total-amount {
            position: relative;
            margin: 0 50px;
            padding: 30px 0;
            text-align: center;

            @include mini-border(bottom, #E4E5E6, after);

            p+p {
                margin-top: 10px;
            }

            .amount-title {
                font-size: 13px;
                color: #999;
                line-height: 18px;
            }

            .amount {
                font-size: 30px;
                line-height: 72px;
                color: #272727;
            }
        }

        .day-group {
            position: relative;
            padding: 30px 60px;
            margin-bottom: 40px;

            .left,
            .right {
                display: inline-block;
                font-size: 13px;
                height: 26px;
                line-height: 26px;
                color: #272727;
            }

            .right {
                float: right;
            }
        }
    }

    .bottom-btn {
        position: fixed;
        bottom: 0px;
        left: 0px;
        right: 0;
        padding: 50px 54px 50px 56px;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        background: #F7F7F7;

        .mint-button {
            -webkit-box-flex: 1;
            -ms-flex: 1;
            flex: 1;
        }

        .mint-button+.mint-button {
            margin-top: 0px;
            margin-left: 40px;
        }
    }

    .no-data {
        text-align: center;
        margin-top: 30px;

        p {
            line-height: 48px;
            font-size: 17px;
            color: #666666;
        }
    }

    .cell {
        height: 150px;

        .value {
            font-family: SFUIText-Regular;
            font-size: 17px;
            color: #272727;
            line-height: 48px;
        }

        .cell-desc {
            font-family: PingFangSC-Regular;
            margin-top: 4px;
            font-size: 13px;
            color: #B5B5B5;
            line-height: 36px;
        }

        .title {
            font-family: PingFangSC-Regular;
            font-size: 16px;
            color: #272727;
            line-height: 44px;
        }

        .cell-label {
            font-family: PingFangSC-Regular;
            margin-top: 10px;
            font-size: 13px;
            color: #B5B5B5;
            line-height: 36px;
        }
    }
</style>


