<template>
    <div class="order-stages-apply">
        <apply ref="child" :dataCopy="infoData" :mapCopy="map"></apply>
        
        <div class="field-action">
            <mt-button type="primary" size="large" @click="confirm">确认申请</mt-button>
        </div>
    </div>
</template>

<script>
    import Apply from '../public/apply';
    export default {
        name: 'order-stages-apply',
        components: {
            Apply
        },
        data() {
            return {
                infoData: {},
                map: [
                    { key: 'AMOUNT', title: '分期金额' },
                    { key: 'num', title: '分期期数' },
                    { key: 'INSTALAMT', title: '每期还款额' },
                    { key: 'FEEAMT', title: '分期手续费' }
                ]
            };
        },
        created() {
            let ls = this.$ls.getItem('credit-order-stages');
            this.infoData = Object.assign({}, ls);
        },
        methods: {
            confirm() {
                // this.$router.push({ name: 'CreditHuiResult' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName']) {
                this.$ls.setItem('credit-order-stages', this.$refs.child.infoData);
            } else {
                this.$ls.removeItem('credit-order-stages');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .field-action {
        clear: both;
        margin-top: 116px;
    }
</style>


