<template>
	<div class="pwd-manage-transaction-success">
		<tip-page :tipTitle="msg" :tipButton="tipButton" :tipTime="tipTime + tipTimeMsg" :tipMsg="tipMsg" @submit="back">
		</tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			tipButton: '',
			tipTime: '',
			tipTimeMsg: '',
			tipMsg: ''
		};
	},
	computed: {
		msg: function(){
			let type = this.$route.params.type;
			if(type === 'apply'){
				return '交易密码申请成功';
			}else if(type === 'update'){
				return '交易密码修改成功';
			}else{
				return '交易密码重置成功';
			}
		}
	},
	created() {
		if(this.$route.params.fromAdd){
			this.tipMsg = '跳转至添加信用卡流程';
			this.tipTimeMsg = '秒后';
			this.times(3);
		} else{
			this.tipButton = '返回信用卡管理';
		}
	},
	methods: {
		back() {
			this.$router.push({name: 'CreditManage'});
		},
		times(time) {
			let self = this;
			this.tipTime = time;
			if(this.tipTime == 1){
				setTimeout(function() {
					self.$router.push({name: 'MyBankCardCreditDetail', params: {card: this.card}});
				}, 1000);
			} else{
				setTimeout(function() {
					self.times(--self.tipTime);
				}, 1000);
			}
		}
	}
};

</script>

<style lang="scss">
.pwd-manage-transaction-success{
}
</style>


