<template>
	<div class="more-service-one-active">
		<div class="field-part">
			<div class="head-hint head-reset">请输入信用卡卡号</div>
			<mt-field v-model="ACCOUNT" label="信用卡卡号" type="text"></mt-field>
		</div>
		<div class="button-bottom">
            <mt-button @click.native="next()" type="primary" size="large">下一步</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			ACCOUNT: ''
		};
	},
	created() {
		let activeCard = this.$ls.getItem('activeCard');
		if(activeCard && activeCard.card){
			this.detail = activeCard;
		}
	},
	methods: {
        next() {
        	if(this.$validate.isEmpty(this.ACCOUNT)) {
                this.$msg('信用卡卡号不能为空');
                return;
            }
        	this.checkCredit();
        },
        checkCredit() {
			service.checkCredit()
			.then((data) => {
				this.$router.push({name: 'OneActiveDetail', params: {ACCOUNT: this.ACCOUNT}});
			}, (err) => {
	            this.$msg('卡号错误，请核对后重新输入');
			});
		}
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
        	console.log(this.detail);
            this.$ls.setItem('activeCard', this.detail);
        }else{
            this.$ls.removeItem('activeCard');
        }
        next( true );
    }
};

</script>

<style lang="scss">
.more-service-one-active{
	.head-reset{
		margin-top: 0;
		padding-top: 20px;
	}
}
</style>


