<template>
	<div class="more-service-one-active-password">
        <div class="field-part">
            <mt-cell title="信用卡卡号" :value="detail.card"></mt-cell>
        </div>
        <div v-if="ISSETPWD == 0" class="field-part">
            <div class="head-hint">请设置查询密码</div>
    		<cell-password :options=" cellPassword1 "></cell-password>
    		<cell-password :options=" cellPassword2 "></cell-password>
        </div>
        <div class="field-part">
            <div class="head-hint">请设置交易密码</div>
    		<cell-password :options=" cellPassword3 "></cell-password>
    		<cell-password :options=" cellPassword4 "></cell-password>
        </div>
		<div class="button-bottom">
            <mt-button @click.native="showDialogSmsVerify()" type="primary" size="large">下一步</mt-button>
            <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			ISSETPWD: 0,
			cellPassword1: {
                title: '查询密码',
                rule: 'number' // number or mix
            },
            cellPassword2: {
                title: '确认查询密码',
                rule: 'number' // number or mix
            },
            cellPassword3: {
                title: '交易密码',
                rule: 'number' // number or mix
            },
            cellPassword4: {
                title: '确认交易密码',
                rule: 'number' // number or mix
            },
            dialogSmsVerify: {
                title: '输入验证码完成激活',
                btnConfirmText: '完成激活',
                btnGetText: '获取验证码'
            }
		};
	},
	methods: {
        showDialogSmsVerify() {
            this.$refs.dialogSmsVerify.show().then((data) => {
                this.activeCredit();
            }, (data) => {
            });
        },
        activeCredit() {
            service.activeCredit()
            .then((data) => {
                this.$router.push({name: 'OneActiveSuccess', params: {fromAdd: this.$route.params.fromAdd}});
            }, (err) => {
                console.log(err);
            });
        }
	},
	created() {
		this.detail = this.$ls.getItem('activeCard');
	}
};

</script>

<style lang="scss">
.more-service-one-active-password{
}
</style>


