<template>
    <div class="public-index">
        <list @emit-href="href" :dataCopy="listData"></list>
    </div>
</template>

<script>
    import * as services from '@/services/credit/credit';
    import List from '../public/list';
    export default {
        name: 'public-index',
        components: {
            List
        },
        data() {
            return {
                listData: []
            };
        },
        created() {
            services.queryConsumeList()
                .then(res => {
                    this.listData = res.data;
                }, err => {
                    console.log(err);
                });
        },
        methods: {
            href(item) {
                let ls = this.$ls.getItem('credit-consume') || {};
                // 只保留上一页面的缓存
                let obj = Object.assign({}, { account: ls.account }, item);
                this.$ls.setItem('credit-consume', obj);
                this.$router.push({ name: 'CreditConsumeSubmit' });
            }
        }
    };

</script>


