<template>
    <div class="consume">
        <index ref="child" ls='credit-consume'>
            <template slot="info">
                <p class="info">您可对单笔满500元(含)以上的消费金额进行分期，并在约定期限内偿还。</p>
            </template>

            <div class="field-action" slot="btn">
                <mt-button type="primary" size="large" @click="doApply">我要申请</mt-button>
            </div>

            <template slot="process">
                <p class="title">
                    办理流程：
                </p>
                <p>
                    刷卡消费>>>持卡人申请(账单生成之前)>>>分期偿还
                </p>
            </template>
            
            <template slot="warn">
                <p class="title">
                    温馨提示：
                </p>
                <p>
                    1、您可选择3、6、9、12、18、24期进行惠消费分期，一个月为一期；
                </p>
                <p>
                    2、惠消费撤销可在申请完成后至账单日之前操作。
                </p>
            </template>
        </index>
    </div>
</template>

<script>
    import Index from '../public';
    export default {
        name: 'consume',
        components: {
            Index
        },
        methods: {
            doApply() {
                this.$router.push({ name: 'CreditConsumeList' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName']) {
                this.$ls.setItem('credit-consume', this.$refs.child.formData);
            } else {
                this.$ls.removeItem('credit-consume');
            }
            next(true);
        }
    };

</script>


