<template>
	<div class="credit-manage-auto-repay">
		<div class="cell-part">
			<mt-cell @click.native="toSet" title="自动还款" :value="detail.isOpen ? '已开启' : '已关闭'" is-link></mt-cell>
		</div>
		<div v-if="!detail.isOpen" class="field-hint">您尚未开通自动还款功能</div>
		<div class="cell-part" v-if="detail.isOpen">
			<div class="head-hint">当前自动还款设置如下</div>
			<mt-cell title="自动还款账号" :value="detail.REPAYACCT"></mt-cell>
			<mt-cell title="自动还款方式" :value="detail.REPAYNAME"></mt-cell>
		</div>
		<mt-button type="primary" @click.native="detail.isOpen = !detail.isOpen">测试</mt-button>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			detail: {
				isOpen: false,
				REPAYACCT: '',
				REPAYNAME: ''
			}
		};
	},
	methods: {
		toSet() {
			let name = this.detail.isOpen ? 'CreditManageAutoRepayOpen' : 'CreditManageAutoRepayNotOpenSet';
			this.$router.push({name: name});
		},
		autoRepaySearch() {
			service.autoRepaySearch({
				OPTION: '1'
			})
			.then(data => {
			}, err => {
				console.log(err);
			});
		}
	},
	created() {
		this.autoRepaySearch();
	}
};

</script>

<style lang="scss">
	
.credit-manage-auto-repay{

}

</style>


