<template>
    <div class="public-index">
        <template v-if="!status">
            <div class="field-part">
                <cell title="信用卡卡号" @click.native="showAccount()" v-model="formData.account" isLink select></cell>
                <selector-payment-account ref="selectorAccount" :options=" selectorAccount "></selector-payment-account>
            </div>

            <slot name="amount"></slot>

            <div class="field-part">
                <div class="panel">
                    <slot name="info"></slot>
                </div>
            </div>

            <slot name="btn"></slot>
        </template>
        <div v-if="status == 'none-login'" class="none-condition">
            <div class="contain contain-login">
                <p>您尚未登录</p>
                <p>无法申请惠分单</p>
            </div>
            <div class="button">
                <mt-button type="primary" size="large">登录</mt-button>
            </div>
        </div>
        <div v-if="status == 'none-credit'" class="none-condition">
            <div class="contain contain-credit">
                <div class="title">
                    <p>您尚未添加信用卡</p>
                    <p>无法申请惠分单</p>
                </div>
                <p class="remark">您可对整个账单中的消费金额进行分期 并在约定期限内偿还</p>
            </div>
            <div class="button">
                <mt-button @click="addCredit" type="primary" size="large">添加信用卡</mt-button>
            </div>
        </div>

        <div class="panel">
            <div class="process">
                <slot name="process"></slot>
            </div>
            <div class="warn">
                <slot name="warn"></slot>
            </div>
        </div>
    </div>
</template>

<script>
    import * as service from '@/services/credit/credit';
    export default {
        name: 'public-index',
        props: ['ls'],
        data() {
            return {
                status: '',
                formData: {
                    account: ''
                },
                selectorAccount: {
                    title: '选择付款账号',
                    type: 'credit',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                }
            };
        },
        created() {
            let ls = this.$ls.getItem(this.ls);
            service.queryCreditCard()
                .then((res) => {
                    this.selectorAccount.accountList = res.data;
                    let _data;
                    if (ls == null) {
                        _data = res.data.filter(item => {
                            return item.ISDEFAULT;
                        })[0] || {};
                    } else {
                        _data = res.data.filter(item => {
                            return item.ACCOUNT === ls.account;
                        })[0] || {};
                    }
                    this.selectorAccount.checked = _data;
                    this.formData.account = this.selectorAccount.checked.ACCOUNT;
                }, (err) => {
                    console.log(err);
                });
        },
        methods: {
            showAccount() {
                this.$refs.selectorAccount.show().then((data) => {
                    this.selectorAccount.checked = data.item;
                    this.formData.account = this.selectorAccount.checked.ACCOUNT;
                }, (data) => {
                });
            },
            addCredit() {
                this.$router.push({ name: 'MyBankCardCreditAdd' });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .amount {
        padding: 40px 0;
        text-align: center;

        .p {
            font-family: PingFangSC-Regular;
            font-size: 13px;
            color: #62481C;
            height: 36px;
            line-height: 36px;
        }

        .num {
            font-family: SFUIDisplay-Regular;
            font-size: 30px;
            color: #745B34;
            height: 72px;
            line-height: 72px;
        }

        .p-num {
            margin-top: 20px;
            font-family: PingFangSC-Regular;
            font-size: 13px;
            color: #705A3A;
            line-height: 36px;
        }
    }

    .panel {
        position: relative;
        padding: 30px 50px;
        background-color: #fff;

        .info {
            font-family: PingFangSC-Regular;
            font-size: 13px;
            color: #666666;
            line-height: 36px;
        }

        .process {
            p {
                font-family: SFUIDisplay-Regular;
                font-size: 13px;
                color: #666;
                line-height: 60px;
            }
            .title {
                font-family: PingFangSC-Regular;
                font-size: 16px;
                color: #272727;
                height: 44px;
                line-height: 44px;
                margin-bottom: 18px;
            }
        }

        .warn {
            margin-top: 30px;

            .title {
                font-family: PingFangSC-Regular;
                font-size: 16px;
                color: #272727;
                height: 44px;
                line-height: 44px;
                margin-bottom: 30px;
            }

            p:not(:first-child) {
                font-size: 13px;
                color: #666666;
                margin-bottom: 10px;
            }
        }
    }

    .none-condition {
        margin-bottom: 40px;
        padding: 48px 0 30px;
        background-color: #fff;
        .contain {
            position: relative;
            padding: 0 342px 0 50px;
            height: 172px;
            &:after {
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                display: block;
                width: 276px;
                height: 172px;
                @include image-set('../../../../assets/img/credit/phone');
            }
        }
        .contain-login {
            p {
                padding-top: 20px;
                height: 80px;
                line-height: 60px;
                font-size: 24px;
                color: #272727;
                @include ellipsis();
            }
        }
        .contain-credit {
            .title {
                p {
                    font-size: 20px;
                    color: #272727;
                    height: 50px;
                    line-height: 40px;
                    @include ellipsis();
                }
            }
            .remark {
                margin-top: 6px;
                font-size: 12px;
                color: #666666;
            }
        }
        .button {
            padding: 50px 50px 0;
        }
    }
</style>


