<template>
	<div class="more-service-quick-loss">
        <div class="cell-part">
            <mt-cell @click.native="showSelectorPaymentAccount()" title="信用卡卡号" placeholder="" :value="PAYER_ACCOUNT" is-link>
            </mt-cell>
        </div>
        <div class="cell-part">
            <cell-password :options=" cellPassword "></cell-password>
        </div>
        <ul class="mark">
            <li>申请卡片挂失可能会产生手续费</li>
            <li>
                <a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
            </li>
        </ul>
        <div class="button-bottom">
            <mt-button @click="submit" type="primary" size="large">确认挂失</mt-button>
        </div>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
	</div>
</template>

<script>

import * as bservice from '@/services/public';
import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			PAYER_ACCOUNT: '',
            cellPassword: {
                title: '查询密码',
                rule: 'number' // number or mix
            },
			selectorPaymentAccount: {
				type: 'credit',
                checked: {},
                showWarn: false,
                accountList: []
            }
		};
	},
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                console.log(data.item);
                this.selectorPaymentAccount.checked = data.item;
                this.PAYER_ACCOUNT = data.item.ACCOUNT;
            }, data => {
            });
        },
        submit() {
        	this.submitQuickLoss();
        },
        toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
        submitQuickLoss() {
            service.submitQuickLoss({
                PAYER_ACCOUNT: this.PAYER_ACCOUNT,
                PAY_PWD: ''
            })
            .then((data) => {
                this.$router.push({name: 'QuickLossSuccess'});
            }, (err) => {
                this.$toast('该卡已挂失');
            });
        },
        getCommonAccountList() {
            bservice.getCommonAccountList()
            .then(data => {
                this.selectorPaymentAccount.accountList = data.LIST;
            }, err => {
                console.log(err);
            });
        }
	},
	created() {
		this.account = this.account || this.$store.state.keepSearch.account;
        this.account = this.$route.params.card;
        this.getCommonAccountList();
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateKeepSearch', {key: 'account', value: this.account});
        }else{
            this.$store.dispatch('updateKeepSearch', {key: 'account', value: ''});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.more-service-quick-loss{
    .mark{
        display: table;
        width: 100%;
        padding: 20px 50px 0px;
        li{
            display: table-cell;
            font-size: 13px;
            color: #B5B5B5;
        }
        li:last-child{
            text-align: right;
        }
    }
	.forget-pwd{
		color: #308CF8;
	}
}
</style>


