<template>
    <div class="withdrawals">
        <index ref="child" ls="credit-withdrawals">
            <div class="amount" slot="amount">
                <p class="p">惠提现可用额度(元)</p>
                <p class="num">{{this.amountData.AVLIMT | formatNumber(2)}}</p>
                <p class="p-num">惠提现额度 {{this.amountData.CREDITLIMIT | formatNumber(2)}} 元</p>
            </div>

            <template slot="info">
                <p class="info">
                    您获得核准的惠提现额度后，可申请转入个人借记卡，并在约定期限内偿还。
                </p>
            </template>
            <div class="field-action" slot="btn">
                <mt-button type="primary" size="large" @click="doApply">我要申请</mt-button>
            </div>
            <template slot="process">
                <p class="title">
                    办理流程：
                </p>
                <p>
                    农商银行核准额度>>>持卡人申请>>>转入借记卡>>>提现>>>分期偿还
                </p>
            </template>
            <template slot="warn">
                <p class="title">
                    温馨提示：
                </p>
                <p>
                    1. 惠提现起点金额为5000元，以1000的倍数递增；
                </p>
                <p>
                    2. 您可选择3、6、9、12、18、24期进行惠提现分期，一个月为一期；
                </p>
                <p>
                    3. 惠提现撤销仅限当日处理。
                </p>
            </template>
        </index>
    </div>
</template>

<script>
    import * as services from '@/services/credit/credit';
    import Index from '../public';
    export default {
        name: 'withdrawals',
        components: {
            Index
        },
        data() {
            return {
                amountData: {
                    CREDITLIMIT: '',    // 信用额度
                    AVLIMT: ''  // 可用额度
                }
            };
        },
        mounted() {
            let params = {
                DJKKH: this.$refs.child.formData.account
            };
            services.queryWithdrawalsLimit(params)
                .then(res => {
                    this.amountData = res.data;
                }, err => {
                    console.log(err);
                });
        },
        methods: {
            doApply() {
                this.$router.push({ name: 'CreditWithdrawalsSubmit' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] === from.meta['groupName']) {
                let obj = Object.assign({}, this.$refs.child.formData, this.amountData);
                this.$ls.setItem('credit-withdrawals', obj);
            } else {
                this.$ls.removeItem('credit-withdrawals');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .amount {
        height: 244px;
        @include image-set('../../../../assets/img/credit/hui-series/gold-bg');
        background-size: 100% 100%;
    }
</style>


