<template>
    <div class="credit-hui-result">
        <div class="hd">
            <i :class="{'icon-success': scccess, 'icon-error': !scccess}"></i>
            <div class="msg" v-text="msg"></div>
        </div>

        <div class="main">
            <cell-map :dataCopy="infoData" :map="map"></cell-map>
        </div>

        <div class="foot">
            <mt-button type="primary" size="large" @click="back('-3')">继续申请惠消费</mt-button>
            <mt-button size="large" @click="back('-4')">返回惠消费首页</mt-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'credit-hui-result',
        data() {
            return {
                scccess: true,
                infoData: {},
                map: [
                    { key: 'account', title: '信用卡卡号' },
                    { key: 'AMOUNT', title: '分期金额', style: { color: '#FF942F' } },
                    { key: 'num', title: '分期数' },
                    { key: 'INSTALAMT', title: '每期还款额' },
                    { key: 'FEEAMT', title: '分期手续费' }
                ]
            };
        },
        created() {
            let ls = this.$ls.getItem('credit-consume');
            console.log(ls);
            this.infoData = ls;
        },
        computed: {
            msg() {
                return this.success ? '惠消费申请成功' : '惠消费申请失败';
            }
        },
        methods: {
            back(p) {
                this.$router.go(p);
            }
        }
    };

</script>

<style lang="scss" scoped>
    .hd {
        padding: 48px 0 40px;
        background-color: #fff;
        text-align: center;

        .icon-success {
            display: inline-block;
            height: 120px;
            width: 120px;
            @include image-set('../../../../assets/img/tip/success');
            background-size: 100% 100%;
        }

        .msg {
            margin-top: 40px;
            font-family: PingFangSC-Medium;
            font-size: 17px;
            color: #272727;
            line-height: 48px;
        }
    }

    .main {
        margin-top: 40px;
    }

    .foot {
        margin: 80px 50px;

        .mint-button+.mint-button {
            margin-top: 30px;
        }
    }
</style>


