<template>
	<div class="more-service-patch-card">
        <div class="cell-part">
            <mt-cell class="allow-down" @click.native="showSelectorPaymentAccount()" title="信用卡卡号" placeholder="" :value="PAYER_ACCOUNT || '请选择'" is-link>
            </mt-cell>
        </div>
        <template>
            <div class="cell-part">
                <mt-cell class="allow-down" title="补卡方式" @click.native=" showSelectorType()  "is-link :value=" TYPE || '请选择' ">
                </mt-cell>
                <mt-cell title="邮寄地址" @click.native=" showSelectorAddress()  "is-link class="address allow-down">
                    <div>
                        <span class="type">{{ADDRESS || '请选择'}}</span><br>
                        <span class="value">安徽省合肥市寿春路C栋2308室安徽省合肥市寿春路C栋2308室</span>
                    </div>
                </mt-cell>
            </div>
            <div class="cell-hint text-right">
                <a @click="toUpdateAddress" href="javascript:;" class="forget-pwd">如果地址有变更，请修改</a>
            </div>
            <div class="cell-part">
                <cell-password :options=" cellPassword "></cell-password>
            </div>
            <ul class="mark">
                <li>申请卡片挂失可能会产生手续费</li>
                <li>
                    <a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
                </li>
            </ul>
            <div class="button-bottom">
                <mt-button @click="submit" type="primary" size="large">确认补卡</mt-button>
            </div>
        </template>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
        <selector-action ref="selectorType" :options=" selectorType "></selector-action>
        <selector-action ref="selectorAddress" :options=" selectorAddress "></selector-action>
    </div>
</template>

<script>

import * as bservice from '@/services/public';
import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			PAYER_ACCOUNT: '',
            TYPE: '',
            TYPE_ID: '',
            ADDRESS: '',
            ADDRESS_ID: '',
            cellPassword: {
                title: '查询密码',
                rule: 'number' // number or mix
            },
			selectorPaymentAccount: {
				type: 'credit',
                checked: {},
                showWarn: false,
                accountList: []
            },
            selectorType: {
                title: '请选择补卡方式',
                key: 'field1',
                value: 'field2',
                checked: '',
                actionList: [
                    {
                        field1: 1,
                        field2: '即将发行'
                    },
                    {
                        field1: 2,
                        field2: '可购买'
                    },
                    {
                        field1: 3,
                        field2: '已发行'
                    }
                ]
            },
            selectorAddress: {
                title: '请选择邮寄方式',
                key: 'field1',
                value: 'field2',
                checked: '',
                actionList: [
                    {
                        field1: 1,
                        field2: '家庭地址'
                    },
                    {
                        field1: 2,
                        field2: '单位地址'
                    }
                ]
            }
		};
	},
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.PAYER_ACCOUNT = data.item.ACCOUNT;
            }, data => {
            });
        },
        submit() {
        	this.submitPatchCard();
        },
        toUpdateAddress() {
            this.$router.push({name: 'CreditManageSetService'});
        },
        toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
        initAccountSelected() {
            let account = this.$route.params.ACCOUNT;
            if(account){
                let accountList = this.selectorPaymentAccount.accountList;
                for(let i = 0; i < accountList.length; i++){
                    if(accountList[i].ACCOUNT == account){
                        this.PAYER_ACCOUNT = account;
                        this.selectorPaymentAccount.checked = accountList[i];
                    }
                }
            }
        },
        submitPatchCard() {
            service.submitPatchCard({
                PAYER_ACCOUN: this.PAYER_ACCOUNT,
                TYPE: this.TYPE_ID,
                ADDRESS: this.ADDRESS_ID,
                PAY_PWD: ''
            })
            .then((data) => {
                this.$router.push({name: 'PatchCardSuccess'});
            }, (err) => {
                this.$alert('该卡片未挂失，不能补卡', {
                    btn: { text: '我知道了' }
                });
            });
        },
        showSelectorType() {
            this.$refs.selectorType.show().then((data) => {
                this.selectorType.checked = data.item.field1;
                this.TYPE = data.item.field2;
                this.TYPE_ID = data.item.field1;
            }, (data) => {
            });
        },
        showSelectorAddress() {
            this.$refs.selectorAddress.show().then((data) => {
                this.selectorAddress.checked = data.item.field1;
                this.ADDRESS = data.item.field2;
                this.ADDRESS_ID = data.item.field1;
            }, (data) => {
            });
        },
        getCommonAccountList() {
            bservice.getCommonAccountList()
            .then(data => {
                this.selectorPaymentAccount.accountList = data.LIST;
                this.initAccountSelected();
            }, err => {
                console.log(err);
            });
        }
	},
	created() {
        this.getCommonAccountList();
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateKeepSearch', {key: 'account', value: this.account});
        }else{
            this.$store.dispatch('updateKeepSearch', {key: 'account', value: ''});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.more-service-patch-card{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
    .mark{
        display: table;
        width: 100%;
        padding: 20px 50px 0px;
        li{
            display: table-cell;
            font-size: 13px;
            color: #B5B5B5;
        }
        li:last-child{
            text-align: right;
        }
    }
	.forget-pwd{
		color: #308CF8;
		font-size: 13px;
	}
    .color-red{
        color: red;
    }
    .address{
        .mint-cell-value{
            font-size: 13px;
            max-width: 65%;
            text-align: right;
            line-height: 36px;
        }
        .type{
            font-size: 15px;
            color: #272727;
        }
        .value{
            color: #B5B5B5;
        }
    }
}
</style>


