<template>
	<div class="credit-manage-card-set">
		<div class="cell-part">
			<mt-cell title="信用卡卡号" :value="detail.ACCOUNT"></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('CreditManageCardSetRename')" title="别名" :value="detail.ALIAS || '未设置'" is-link></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('CreditManageCardSetCost')" title="消费凭密" :value="detail.PINCHK" is-link></mt-cell>
		</div>
		<div class="cell-part">
			<mt-cell @click.native="toSet('QuickLoss')" title="快速挂失" is-link></mt-cell>
			<mt-cell @click.native="toSet('PatchCard')" title="闪电补卡" is-link></mt-cell>
		</div>
		<div class="delete-button">
			<a @click="submit" href="javascript:;">从手机银行删除此卡</a>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			detail: {}
		};
	},
	created() {
		let detail = this.$route.params.detail;
		if(detail){
			this.$ls.setItem('creditMsg', this.$route.params.detail);
		}
		this.detail = this.$ls.getItem('creditMsg');
	},
	methods: {
		toSet(name) {
			this.$router.push({
				name: name,
				params: {
					detail: {
						ACCOUNT: this.detail.ACCOUNT,
						ALIAS: this.detail.ALIAS
					}
				}
			});
		},
		submit() {
			console.log(this.detail.ACCOUNT);
			let account = this.detail.ACCOUNT.substring((this.detail.ACCOUNT.length - 4), this.detail.ACCOUNT.length);
			this.$confirm(`确定要删除尾号为${account}的${this.detail.JGMC}信用卡吗？`, {
                yes: { text: '确认删除' },
                no: { text: '取消' }
            }).then((result)=>{
				this.deleteCredit();
            }, (result)=>{
                console.log(result);
            });
		},
		deleteCredit() {
			service.deleteCredit({
				ACCT_TYPE: '3',
				PAYER_ACCOUNT: this.PAYER_ACCOUNT,
				OPT_TYPE: '2'
			})
			.then((data) => {
				this.$router.push({name: 'CreditManageCardSetSuccess', params: {type: 'delete'}});
			}, (err) => {
				console.log(err);
			});
		}

	}
};

</script>

<style lang="scss">
	
.credit-manage-card-set{
	.delete-button{
		margin-top: 80px;
		padding: 24px 0;
		text-align: center;
		background-color: #fff;
		a{
			color: #FF511A;
			font-size: 15px;
		}
	}
}

</style>


