<template>
    <div class="bill">
        <div class="field-part">
            <cell title="信用卡卡号" @click.native="showCreditAccount()" v-model="formData.PAYER_ACCOUNT" isLink select></cell>
            <cell title="预借现金可用额度">
                <div slot="value">{{formData.BORCAAVAL | formatNumber(2)}}元</div>
            </cell>
            <selector-payment-account ref="creditAccount" :options=" creditAccount "></selector-payment-account>
        </div>

        <div class="field-part">
            <mt-field label="转出金额" v-model="formData.AMOUNT"></mt-field>
        </div>

        <div class="field-part">
            <mt-cell title='借记卡卡号' class="allow-down" :value='formData.PAYEE_ACCOUNT' @click.native="showDebitAccount()" is-link>
            </mt-cell>
            <selector-payment-account ref="debitAccount" :options=" debitAccount "></selector-payment-account>
        </div>

        <div class="field-action">
            <mt-button :disabled="!clickable" type="primary" size="large" @click="submit">下一步</mt-button>
        </div>

    </div>
</template>

<script>
    import * as servicesC from '@/services/credit/credit';
    import * as services from '@/services/bank/selector';
    export default {
        name: 'bill',
        data() {
            return {
                formData: {
                    PAYER_ACCOUNT: '',
                    ACCT_NAME: '',
                    BORCAAVAL: '',
                    PAYEE_ACCOUNT: '',
                    COR_NAME: '',
                    AMOUNT: ''
                },
                infoData: {},
                creditAccount: {
                    title: '选择信用卡',
                    type: 'credit',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                },
                debitAccount: {
                    title: '选择借记卡',
                    type: 'payment',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                },
                clickable: false
            };
        },
        watch: {
            'formData': {
                handler: 'validate',
                deep: true
            }
        },
        created() {
            let ls = this.$ls.getItem('credit-cash-advance');
            if (ls != null) {
                this.formData.AMOUNT = ls.AMOUNT;
            }
            servicesC.queryCreditCard()
                .then((res) => {
                    this.creditAccount.accountList = res.data;
                    let _data;
                    if (ls == null) {
                        _data = res.data.filter(item => {
                            return item.ISDEFAULT;
                        })[0] || {};
                    } else {
                        _data = res.data.filter(item => {
                            return item.ACCOUNT === ls.PAYER_ACCOUNT;
                        })[0] || {};
                    }
                    this.creditAccount.checked = _data;
                    this.formData.PAYER_ACCOUNT = this.creditAccount.checked.ACCOUNT;
                    this.formData.ACCT_NAME = this.creditAccount.checked.NAME;
                    this.queryCardInfo();
                }, (err) => {
                    console.log(err);
                });
            services.getAccountList()
                .then((res) => {
                    this.debitAccount.accountList = res.data;
                    let _data;
                    if (ls == null) {
                        _data = res.data.filter(item => {
                            return item.isDefault === true;
                        })[0] || {};
                    } else {
                        _data = res.data.filter(item => {
                            return item.ACCOUNT === ls.PAYEE_ACCOUNT;
                        })[0] || {};
                    }
                    this.debitAccount.checked = _data;
                    this.formData.PAYEE_ACCOUNT = this.debitAccount.checked.ACCOUNT;
                    this.formData.COR_NAME = this.debitAccount.checked.name;
                }, (err) => {
                    console.log(err);
                });
        },
        methods: {
            showCreditAccount() {
                this.$refs.creditAccount.show().then((data) => {
                    this.creditAccount.checked = data.item;
                    this.formData.PAYER_ACCOUNT = this.creditAccount.checked.ACCOUNT;
                    this.queryCardInfo();
                }, (data) => {
                });
            },
            showDebitAccount() {
                this.$refs.debitAccount.show().then((data) => {
                    this.debitAccount.checked = data.item;
                    this.formData.PAYEE_ACCOUNT = this.debitAccount.checked.ACCOUNT;
                }, (data) => {
                });
            },
            queryCardInfo() {
                servicesC.queryCreditList()
                    .then(res => {
                        let _data = res.data.filter(item => {
                            return item.account === this.formData.PAYER_ACCOUNT;
                        })[0] || {};
                        this.formData.BORCAAVAL = _data.BORCAAVAL;
                    }, err => {
                        console.log(err);
                    });
            },
            validate(val) {
                if (this.$validate.isEmpty(val.AMOUNT)) {
                    this.clickable = false;
                    return false;
                }
                this.clickable = true;
            },
            submit() {
                this.$ls.setItem('credit-cash-advance', this.formData);
                this.$router.push({ name: 'CreditCashAdvanceApply' });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName']) {
                this.$ls.removeItem('credit-cash-advance');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .field-action {
        margin-top: 80px;
    }
</style>


