<template>
    <div class="repayment-self-pay">
        <pay :dataCopy="infoData" :mapCopy="map">
            <template slot="btn">
                <mt-button size="large" type="primary" @click="submit">确认还款</mt-button>
            </template>
        </pay>

        <dialog-sms-verify ref="dialogSmsVerify" :options=" dialogSmsVerify "></dialog-sms-verify>
    </div>
</template>

<script>
    import * as services from '@/services/credit/credit';
    import Pay from '../pay';
    export default {
        name: 'repayment-self-pay',
        components: {
            Pay
        },
        data() {
            return {
                map: [
                    { key: 'PAYER_ACCOUNT', title: '信用卡卡号' },
                    { key: 'PAYEE_ACCOUNT', title: '借记卡卡号' },
                    { key: 'AMOUNT', title: '还款金额', type: 'amount-unit', style: { color: '#FF942F' } }
                ],
                dialogSmsVerify: {
                    title: '输入验证码完成还款',
                    btnConfirmText: '完成还款',
                    btnGetText: '获取验证码'
                }
            };
        },
        created() {
            let ls = this.$ls.getItem('credit-repayment');
            this.infoData = ls;
        },
        methods: {
            submit() {
                this.$refs.dialogSmsVerify.show().then((data) => {
                    services.doRepayment()
                        .then(res => { }, err => {
                            console.log(err);
                        });
                }, (data) => {
                });
            }
        }
    };

</script>


