<template>
    <div class="bill">
        <div class="field-part account-select">
            <cell title="信用卡卡号" @click.native="showAccount()" v-model="formData.account" isLink select></cell>
            <selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount "></selector-payment-account>
        </div>

        <div class="bill-card">
            <div class="hd">
                <p class="bill-day">账单日：{{formData.bill_date}}</p>
                <p class="repayment-day">还款日：{{formData.refund_date}}</p>
            </div>
            <div class="main">
                <p class="now-repayment">本期应还（元）</p>
                <p class="now-amount">{{formData.current_refund_amount | formatNumber(2)}}</p>
            </div>

            <div class="main-foot">
                <p class="name" @click="currentBill">
                    本期账单明细
                </p>
                <i class="arrow"></i>
            </div>

            <div class="foot">
                <p class="rest-amount">
                    剩余未还：{{formData.residue_refund_amount | formatNumber(2)}}元
                </p>
                <i class="icon-warn" @click="warn"></i>
                <p class="low-amount">
                    最低还款：{{formData.low_refund | formatNumber(2)}}元
                </p>
            </div>
        </div>

        <div class="btn-group">
            <mt-button type="primary" size="large" @click="href('CreditRepayment')">我要还款</mt-button>
            <mt-button size="large" @click="href('CreditOrderStages')">惠分单</mt-button>
        </div>



        <div class="field-part">
            <mt-cell title="未出账单" :to="{name: 'CreditBillOut'}" is-link></mt-cell>
            <mt-cell title="历史账单" :to="{name: 'CreditBillHistory'}" is-link></mt-cell>
        </div>
    </div>
</template>

<script>
    import * as service from '@/services/credit/credit';
    export default {
        name: 'bill',
        data() {
            return {
                formData: {
                    account: ''
                },
                selectorPaymentAccount: {
                    title: '选择付款账号',
                    type: 'credit',
                    checked: {},
                    showAddBtn: true,
                    showWarn: false,
                    accountList: []
                }
            };
        },
        created() {
            service.queryCreditCard()
                .then((res) => {
                    this.selectorPaymentAccount.accountList = res.data;
                    let _data = res.data.filter(item => {
                        return item.ISDEFAULT === true;
                    })[0] || {};
                    this.selectorPaymentAccount.checked = _data;
                    this.formData.account = _data.ACCOUNT;
                    let params = {
                        account: this.formData.account
                    };
                    this.getCreditInfo(params)
                        .then(res => {
                            this.formData = res;
                        }, err => {
                            console.log(err);
                        });
                }, (err) => {
                    console.log(err);
                });
        },
        methods: {
            getCreditInfo(params) {
                let self = this;
                return new Promise((resolve, reject) => {
                    service.queryCreditList()
                        .then(res => {
                            let _data = res.data.filter(item => {
                                return item.account === self.formData.account;
                            })[0] || {};
                            resolve(_data);
                        }, (err) => {
                            reject(err);
                        });
                }, err => {

                });
            },
            showAccount() {
                this.$refs.selectorPaymentAccount.show().then((data) => {
                    this.selectorPaymentAccount.checked = data.item;
                    this.formData.account = this.selectorPaymentAccount.checked.ACCOUNT;
                    this.getCreditInfo()
                        .then(res => {
                            this.formData = res;
                        }, err => {
                            console.log(err);
                        });
                }, (data) => {
                });
            },
            currentBill() {
                this.$router.push({ name: 'CreditBillCurrent' });
            },
            href(name) {
                this.$router.push({ name: name });
            },
            warn() {
                this.$hybrid.call({
                    method: 'alert',
                    params: {
                        title: '',
                        message: '在还款日之前偿还最低还款额不会影响您的信用记录',
                        confirmButtonText: '我知道了'
                    },
                    callback: (params) => {
                        console.log(this);
                        console.log(params);
                    }
                });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .bill-card {
        position: relative;
        margin: 30px;
        height: 402px;
        @include image-set('../../../assets/img/credit/bill/bill-card');
        background-size: 100% 100%;

        .hd {
            padding: 24px 30px 0;

            .bill-day {
                display: inline-block;
                line-height: 36px;
                font-size: 13px;
                color: #62481C;
            }

            .repayment-day {
                display: inline-block;
                float: right;
                font-size: 13px;
                line-height: 36px;
                color: #62481C;
            }
        }

        .main {
            text-align: center;
            margin: 44px 0 0;

            .now-repayment {
                font-size: 13px;
                color: #62481C;
            }

            .now-amount {
                font-size: 30px;
                color: #745B34;
                line-height: 72px;
            }
        }

        .main-foot {
            margin-top: 30px;
            text-align: center;

            p {
                display: inline-block;
                font-size: 13px;
                color: #705A3A;
            }

            .arrow {
                margin-left: 11px;
                display: inline-block;
                height: 20px;
                width: 20px;
                @include image-set('../../../assets/img/credit/to');
                background-size: 100% 100%;
            }
        }

        .foot {
            position: absolute;
            left: 30px;
            right: 30px;
            bottom: 30px;

            .rest-amount {
                display: inline-block;
                color: #666;
                font-size: 13px;
                line-height: 36px;
            }

            .low-amount {
                display: inline-block;
                float: right;
                color: #666;
                font-size: 13px;
                line-height: 36px;
            }

            .icon-warn {
                display: inline-block;
                float: right;
                margin-top: 2px;
                margin-left: 10px;
                height: 28px;
                width: 28px;
                @include image-set('../../../assets/img/credit/notice');
            }
        }
    }

    .btn-group {
        margin: 30px 50px 40px;

        .mint-button+.mint-button {
            margin-top: 20px;
        }
    }
</style>


