<template>
	<div class="set-service-update">
		<div class="field-part">
	        <mt-cell class="allow-down" title="账单寄送方式" @click.native=" showSelectorSend()  "is-link :value=" detail.BILLSENDTYPE || '请选择' ">
	        </mt-cell>
	        <mt-field v-model="detail.EMAIL" label="电子邮箱" placeholder="请输入电子邮箱":attr="{ maxlength: 50 }"></mt-field>
		</div>
		<div class="field-part">
	        <mt-cell class="allow-down" title="邮寄地址" @click.native=" showSelectorAddress()  "is-link :value=" detail.STSMAIL || '请选择' ">
	        </mt-cell>
	        <mt-field v-model="detail.HOMEADSS" label="家庭地址" placeholder="请输入家庭地址":attr="{ maxlength: 150 }"></mt-field>
	        <mt-field v-model="detail.UNITADSS" label="单位地址" placeholder="请输入单位地址":attr="{ maxlength: 150 }"></mt-field>
		</div>
		<div class="field-part">
        	<cell-password :options=" cellPassword "></cell-password>
		</div>
        <div class="field-warn text-right">
        	<a @click="toResetPwd" href="javascript:;" class="forget-pwd">忘记查询密码？</a>
        </div>
        <div class="button-bottom">
			<mt-button @click.native="update" type="primary" size="large">保存</mt-button>
        </div>
        <selector-action ref="selectorSend" :options=" selectorSend "></selector-action>
        <selector-action ref="selectorAddress" :options=" selectorAddress "></selector-action>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			detail: {
				BILLSENDTYPE: '',
                BILLSENDTYPE_ID: '',
				EMAIL: '',
				STSMAIL: '',
                STSMAIL_ID: '',
				HOMEADSS: '',
				UNITADSS: ''
			},
			cellPassword: {
                title: '查询密码',
                rule: 'number' // number or mix
            },
            selectorSend: {
                title: '请选择账单寄送方式',
                key: 'field1',
                value: 'field2',
                checked: '',
                actionList: [
                    {
                        field1: 1,
                        field2: '电子邮件'
                    },
                    {
                        field1: 2,
                        field2: '非电子邮件'
                    }
                ]
            },
            selectorAddress: {
                title: '请选择邮寄地址',
                key: 'field1',
                value: 'field2',
                checked: '',
                actionList: [
                    {
                        field1: 1,
                        field2: '家庭地址'
                    },
                    {
                        field1: 2,
                        field2: '单位地址'
                    }
                ]
            }
		};
	},
	methods: {
		toResetPwd() {
            this.$router.push({name: 'CreditManagePwdSearchReset'});
        },
		update() {
			this.updateSetService();
		},
		updateSetService() {
			service.updateSetService({
                OPER_TYPE: '2',
                BILLSENDTYPE: this.detail.BILLSENDTYPE_ID,
                EMAIL: this.detail.EMAIL,
                STSMAIL: this.detail.STSMAIL_ID,
                HOMEADSS: this.detail.HOMEADSS,
                UNITADSS: this.detail.UNITADSS,
                PAY_PWD: ''
            })
			.then((data) => {
				this.$router.push({name: 'CreditManageSetServiceSuccess'});
			}, (err) => {
				console.log(err);
			});
		},
		showSelectorSend() {
            this.$refs.selectorSend.show().then((data) => {
                this.selectorSend.checked = data.item.field1;
                this.detail.BILLSENDTYPE = data.item.field2;
                this.detail.BILLSENDTYPE_ID = data.item.field1;
            }, (data) => {
            });
        },
        showSelectorAddress() {
            this.$refs.selectorAddress.show().then((data) => {
                this.selectorAddress.checked = data.item.field1;
                this.detail.STSMAIL = data.item.field2;
                this.detail.STSMAIL_ID = data.item.field1;
            }, (data) => {
            });
        }
	}
};

</script>

<style lang="scss">
	
.set-service-update{
	.mint-cell-title,{
		width: 200px;
	}
	.mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
    .forget-pwd{
		color: #308cf8;
		font-size: 13px;
	}
}

</style>


