<template>
    <div class="current">
        <bill :option="options">
            <div slot="btn">
                <div class="bottom-btn">
                    <mt-button size="large" type="primary" @click="doHref('CreditRepayment')">我要还款</mt-button>
                    <mt-button size="large" @click="doHref('CreditOrderStages')">
                        <span class="title">惠分单 </span>
                        <span class="desc"> | 账单分期</span>
                    </mt-button>
                </div>
            </div>
        </bill>
    </div>
</template>

<script>
    import Bill from '../bill';
    import * as services from '@/services/credit/credit';
    export default {
        name: 'current',
        components: {
            Bill
        },
        data() {
            return {
                options: {
                    code: true,
                    bill_date: '',
                    refund_date: '',
                    total_amount: '',
                    list: null
                }
            };
        },
        created() {
            let params = {
                showLoading: '请等待...',
                DJKKH: '',
                ZDNY: ''
            };
            services.queryCurrentBill(params)
                .then(res => {
                    let _data = res.data;
                    if (_data.RESPCODE !== 0) {
                        this.options.code = false;
                        this.options.bill_date = _data.bill_date;
                        this.options.refund_date = _data.refund_date;
                        this.options.total_amount = _data.BILLAMT;
                        this.options.list = _data.list;
                    }
                });
        },
        methods: {
            doHref(name) {
                this.$router.push({ name: name });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .title {
        font-size: 15px;
        color: #272727;
        line-height: 42px;
    }

    .desc {
        opacity: 0.8;
        font-size: 13px;
        color: #272727;
        line-height: 36px;
    }
</style>


