<template>
	<div class="more-service-one-active-detail">
        <div class="field-part">
            <mt-cell title="信用卡卡号" :value="detail.ACCOUNT"></mt-cell>
        </div>
        <div class="field-part">
            <div class="head-hint">请输入卡片信息</div>
    		<mt-field v-model="detail.CVN2" label="CNV2" placeholder="卡片背面签名栏后3位数字":attr="{ maxlength: 3 }"></mt-field>
    		<mt-field v-model="detail.EXPDATE" label="卡片有效期" placeholder="卡片正面带'/'的4位数字，如0619":attr="{ maxlength: 4 }"></mt-field>
    		<mt-field v-model="detail.CRETIFY_NO" label="身份证号码" placeholder="请输入身份证号码":attr="{ maxlength: 18 }"></mt-field>
        </div>
		<div class="button-bottom">
            <mt-button @click.native="next()" type="primary" size="large" :disabled="forbid">下一步</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/more-service';

export default{
	data() {
		return {
			detail: {
				ACCOUNT: '',
				CVN2: '',
				EXPDATE: '',
				CRETIFY_NO: ''
			},
            forbid: true
		};
	},
    watch: {
        detail: {
            handler(item){
                this.forbid = !(item.CVN2 && item.EXPDATE && item.CRETIFY_NO);
            },
            deep: true
        }
    },
	methods: {
        next() {
        	this.checkCreditMessage();
        },
        checkCreditMessage() {
            service.checkCreditMessage({
                ACCOUNT: this.detail.ACCOUNT,
                CVN2: this.detail.CVN2,
                EXPDATE: this.detail.EXPDATE,
                CRETIFY_NO: this.detail.CRETIFY_NO
            })
            .then((data) => {
                this.$router.push({name: 'OneActivePassword', params: {fromAdd: this.$route.params.fromAdd}});
            }, (err) => {
                this.$msg('卡片激活信息有误，请认真核对并重新输入');
            });
        }
	},
	created() {
        if(this.$route.params.ACCOUNT){
            this.detail.ACCOUNT = this.$route.params.ACCOUNT;
        }
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$ls.setItem('activeCard', this.detail);
        }else{
            this.$ls.removeItem('activeCard');
        }
        next( true );
    }
};

</script>

<style lang="scss">
.more-service-one-active-detail{
}
</style>


