<template>
    <div class="public-index">
        <submit ref="child" ls="credit-consume" :dataCopy="infoData" :mapCopy="map"></submit>

        <div class="agree">
            <template>
                <input type="checkbox" id="check" class="box" v-model="agree">
                <label for="check" class="label"></label>
            </template>
            <span class="promise">我已阅读并同意</span>
            <span class="item" @click="showAgreement">惠消费分期协议</span>
        </div>

        <div class="field-action" slot="btn">
            <mt-button :disabled="!agree" type="primary" size="large" @click="submit">提交申请</mt-button>
        </div>

        <dialog-agreement ref="agreement" :options="agreeOptions"></dialog-agreement>
    </div>
</template>

<script>
    import * as services from '@/services/credit/credit';
    import Submit from '../public/submit';
    import Html from './agreement';
    export default {
        name: 'public-index',
        components: {
            Submit
        },
        data() {
            return {
                infoData: {},
                map: [
                    { key: 'PURDATE1', title: '交易日期' },
                    { key: 'AMOUNT', title: '交易金额', type: 'amount-unit', style: { color: '#FF942F' } }
                ],
                agree: false,
                agreeOptions: {
                    title: '惠消费分期协议',
                    btnConfirmText: '同意协议并提交惠消费申请'
                }
            };
        },
        created() {
            let ls = this.$ls.getItem('credit-consume');
            this.infoData = ls;
        },
        methods: {
            showAgreement() {
                this.$refs.agreement.show(Html)
                    .then(data => {
                        this.agree = true;
                    }, data => { });
            },
            submit() {
                services.queryConsumeApply()
                    .then(res => {
                        let ls = this.$ls.getItem('credit-consume') || {};
                        let obj = Object.assign({}, ls, this.$refs.child.dataForm, res.data);
                        this.$ls.setItem('credit-consume', obj);
                        this.$router.push({ name: 'CreditConsumeApply' });
                    }, err => {
                        console.log(err);
                    });
            }
        },
        beforeRouteLeave(to, from, next) {
            if (to.meta['groupName'] !== from.meta['groupName']) {
                this.$ls.removeItem('credit-consume');
            }
            next(true);
        }
    };

</script>

<style lang="scss" scoped>
    .agree {
        margin: 40px 0 0 50px;

        .box+.label {
            position: relative;
            display: inline-block;
            margin-right: 20px;
            height: 39px;
            width: 39px;
            border: 1px solid #559BDF;
            border-radius: 2px;
            vertical-align: middle;
        }
        /**
         * 隐藏默认的checkbox
         */
        .box {
            display: none;
        }

        .box:checked+label:after {
            content: '\2714';
            position: absolute;
            top: 0px;
            left: 0px;
            color: #559BDF;
            width: 100%;
            text-align: center;
            font-size: 13px;
            vertical-align: text-top;
        }

        span {
            font-family: PingFangSC-Regular;
            font-size: 13px;
            color: #666;
            line-height: 36px;
        }

        .item {
            color: #559BDF;
        }
    }

    .field-action {
        margin-top: 84px;
    }
</style>


