<template>
	<div class="more-service-patch-card-success">
		<tip-page tipTitle="补卡成功" tipButton="返回卡片设置" @submit="back">
			<div class="card-contain">
				<div class="card-msg">
					<div class="line1">
						<div class="title">新信用卡卡号</div>
						<div class="value">{{detail.ACCOUNT}}</div>
					</div>
					<div class="line2">
						<div class="title">邮寄地址</div>
						<div class="value">{{detail.ADDRESS}}</div>
					</div>
				</div>
			</div>
		</tip-page>
	</div>
</template>

<script>
	
export default {
	data() {
		return {
			detail: {}
		};
	},
	methods: {
		back() {
			this.$router.push({name: 'Credit'});
		}
	}
};

</script>

<style lang="scss">
.more-service-patch-card-success{
	.card-contain{
		margin: 30px 40px 80px;
		.card-msg{
			padding: 40px 50px;
			font-size: 15px;
			color: #666666;
			border-radius: 8px;
			background-color: #fff;
			@include image-set('../../../../assets/img/credit/card');
			background-size: auto 100%;
			box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.06);
			> div{
				.title{
					float: left;
				}
				.value{
					float: right;
					max-width: 380px;
					color: #272727;
					text-align: right;
				}
			}
			.line2{
				margin: 20px 0 0;
			}
			> div:after{
				content: "clear"; 
				display: block; 
				height: 0; 
				clear: both; 
				overflow: hidden; 
				visibility: hidden;
			}
		}
	}
}
</style>


