<template>
    <div class="current">
        <bill :option="options">
            <div slot="btn">
                <div class="bottom-btn field-action">
                    <mt-button size="large" @click="doHref('CreditConsume')">
                        <span class="title">惠消费 </span>
                        <span class="desc"> | 灵活分期</span>
                    </mt-button>
                </div>
            </div>
        </bill>
    </div>
</template>

<script>
    import Bill from '../bill';
    import * as services from '@/services/credit/credit';
    export default {
        name: 'current',
        components: {
            Bill
        },
        data() {
            return {
                options: {
                    code: true,
                    list: null
                }
            };
        },
        created() {
            let params = {
                showLoading: '请等待...',
                DJKKH: ''
            };
            services.queryOutBill(params)
                .then(res => {
                    let _data = res.data;
                    if (_data.code !== 0) {
                        console.log(_data);
                        this.options.code = false;
                        this.options.list = _data.list;
                    }
                });
        },
        methods: {
            doHref(name) {
                this.$router.push({ name: name });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .title {
        font-size: 15px;
        color: #272727;
        line-height: 42px;
    }

    .desc {
        opacity: 0.8;
        font-size: 13px;
        color: #272727;
        line-height: 36px;
    }
</style>


