<template>
	<div class="credit-manage-card-set-rename">
		<div class="field-part">
			<mt-field v-model="detail.ALIAS" label="别名" placeholder="请输入别名，例如：理财卡"></mt-field>
		</div>
		<div class="button-bottom">
			<mt-button @click.native="submit" size="large" type="primary" :disabled="!detail.ALIAS">保存</mt-button>
		</div>
	</div>
</template>

<script>

import service from '@/services/credit/manage';

export default {
	data() {
		return {
			detail: {
				ALIAS: '',
				ACCOUNT: ''
			}
		};
	},
	created() {
		this.detail = this.$route.params.detail;
	},
	methods: {
		submit() {
			if(this.detail.ALIAS && !this.$validate.isAlias(this.detail.ALIAS)){
				this.$alert('别名输入格式不正确，请输入数字、字母或汉字', {
                    btn: { text: '确定' }
                });
                return;
			}
			this.setCreditAlias();
		},
		setCreditAlias() {
			service.setCreditAlias({
				ACCOUNT: this.detail.ACCOUNT,
				ALIAS: this.detail.ALIAS
			})
			.then((data) => {
				this.$router.push({name: 'CreditManageCardSetSuccess', params: {type: 'rename'}});
			}, (err) => {
				console.log(err);
			});
		}
	}
};

</script>


